import LDropDown from '@/components/forms/lDropDown/LDropDown.vue'
import LLoading1 from '@/components/global/lLoading1/LLoading1'
import YAlert from '@/components/global/yAlert/YAlert'
import ImageUploader from 'vue-image-upload-resize/src/components/ImageUploader'
export default {
  name: 'citeForm',
  components: {
    LDropDown,
    LLoading1,
    YAlert,
    ImageUploader
  },
  props: {
    info: Object
  },
  data () {
    return {
      type: 'add',
      Display: false,
      loading: false,
      nom: '',
      location: '',
      description: ''
    }
  },
  computed: {
    title () {
      return this.type === 'add' ? 'Ajouter une cité' : 'Modifier cité '+ this.info.nom
    }
  },
  methods: {
    display(type) {
      if(type === 'modify'){
        this.nom = this.info.nom
        this.location = this.info.location
        this.description = this.info.description
      }
      this.type = type
      this.Display = true
    },
    close () {
      this.$refs.yAlertS.Display = false
      this.$refs.yAlert.Display = false
      this.nom = ''
      this.location = ''
      this.description = ''
      this.Display = false
    },
    publish () {
      if(this.type === 'add') this.addCite()
      else this.modifyCite()
    },
    validation () {
      return this.nom === ''
    },
    addCite (){
      if(this.validation()) {
        this.$refs.yAlert.display('Veuillez entrer au moins le nom!')
        return
      }
      this.loading = true
      const axios = require('axios')
      axios.post(this.$store.state.baseUrl + 'cites.php', {
        type: 'add',
        nom:this.nom,
        location:this.location,
        description:this.description
      })
        .then(({data}) => {
          if(data === 'exist') {
            this.loading = false
            this.$refs.yAlert.display('Le nom est déja utilisé!')
          }
          else {
            this.loadData()
          }
        })
        .catch((error) => {
          console.log(error)
          this.loading = false
        })
    },
    modifyCite (){
      if(this.validation()) {
        this.$refs.yAlert.display('Veuillez entrer au moins le nom!')
        return
      }
      this.loading = true
      const axios = require('axios')
      axios.post(this.$store.state.baseUrl + 'cites.php', {
        type: 'modify',
          id: this.info.id,
          nom:this.nom,
          location:this.location,
          description:this.description
      })
        .then(({data}) => {
          if(data === 'exist') {
            this.loading = false
            this.$refs.yAlert.display('Le nom est déja utilisé!')
          }
          else {
            this.loadData()
          }
        })
        .catch((error) => {
          console.log(error)
          this.loading = false
        })
    },
    loadData () {
      const axios = require('axios')
      axios.post(this.$store.state.baseUrl + 'update.php', {
        type:'get'
      })
      .then(({data}) => {
        this.$store.commit('loadCommit',data)
        this.loading = false
        this.$refs.yAlertS.display('Enregistré!')
      })
      .catch((error) => {
        console.log(error)
      })
    }
  }
}
