import LLoading1 from '@/components/global/lLoading1/LLoading1'
import YAlert from '@/components/global/yAlert/YAlert'
import YesNo from '@/components/global/yesNo/YesNo1'
export default {
    name: 'payments',
    components: {
        LLoading1,
        YAlert,
        YesNo
    },
    data () {
        return {
            loading: false,
            payments: [],
            toSend:{id:0,montant:0,contrat:0},
            contrat:'',
            payment:'',
            infos:[]
        }
    },
    methods: {
        dateFormat (dt) {
            let a = new Date(dt)
            let year = a.getFullYear()
            let month = a.getMonth()+1
            let day = a.getDate();

            month = (month < 10)? '0'+ month : month;
            day= (day < 10)? '0' + day : day;
            return day + ' ' + this.$store.state.mois[month -1] + ' ' + year
        },
        setContrat () {
            let charges = this.$store.state.load.charges 
            let contrats = this.$store.state.load.contrats
            let i,j,result=[], max = contrats.length,max0 = charges.length
            for(j=0; j<max0; j++) {
                for(i=0; i<max; i++) {
                    if(contrats[i].id == charges[j].contrat) {
                        result.push({...contrats[i],montant:charges[j].montant,
                        date:charges[j].date,chargeId:charges[j].id,reason:charges[j].raison})
                    }
                }
            }
            this.infos = result
        },
        showYesNo (id,montant,contrat) {
            this.toSend.id = id
            this.toSend.montant = montant
            this.toSend.contrat = contrat
            this.$refs.yesNo.display('Voulez vous vraiment éffectuer cette opération ? C\'est une opération critique')
        },
        yesNoAction (a) {
            if(a=='yes') this.supprimer()
        },
        supprimer () {
            this.loading = true
            const axios = require('axios')
            axios.post(this.$store.state.baseUrl + 'charge.php', {
              type:'delete',
              id: this.toSend.id,
              montant: this.toSend.montant,
              contrat: this.toSend.contrat
            })
            .then(({data}) => {
                console.log(data)
                this.loadData()
            })
            .catch((error) => {
                this.loading = false
                console.log(error)
            })
        },
        loadData () {
            const axios = require('axios')
            axios.post(this.$store.state.baseUrl + 'update.php', {
              type:'get'
            })
            .then(({data}) => {
              this.$store.commit('loadCommit',data)
              this.loading = false
              this.setContrat()
              this.$refs.yAlertS.display('Enregistré!')
            })
            .catch((error) => {
              console.log(error)
            })
        }
    },
    mounted () {
        this.setContrat() // gather the datas listed
    }
}