import LDropDown from '@/components/forms/lDropDown/LDropDown.vue'
import LLoading1 from '@/components/global/lLoading1/LLoading1'
import YAlert from '@/components/global/yAlert/YAlert'
import YesNo from '@/components/global/yesNo/YesNo1'
export default {
  name: 'chargeForm',
  components: {
    LDropDown,
    LLoading1,
    YAlert,
    YesNo
  },
  data () {
    return {
      loading: false,
      locataires: this.$store.state.load.locataires.length == 0 ? [{nom:''}] :this.$store.state.load.locataires,
      chambres: [{nom:''}],
      chargeElt:[{nom:'Charge',description:'montant ajouté au loyer ex: pour l\'eau, electri.'},
                {nom:'Décharge',description:'montant retiré au loyer ex:réduction d\'un mois'}],
      description:'',// chare/decharge description
      contrats: [],
      contrat: [],
      loyer: '',
      amount: 0,
      reason:'',
      dateDebut: '',
      chargeDate:''
    }
  },
  methods: {
    validation () {
      let d = new Date()
      let d1 = new Date(this.chargeDate)
      if((d.getFullYear()<d1.getFullYear()) ||(d.getMonth()<d1.getMonth()) ||(d.getDate() < d1.getDate()) ){
        return true
      }

      let result = this.amount === ''  || this.$refs.chambre.current.nom === '' || this.amount < 0 
      || this.chargeDate == '' || this.reason == ''
      return result
    },
    showYesNo () {
      this.$refs.yesNo.display('Voulez vous vraiment éffectuer cette opération ? Le montant à payer pour ce mois sera le loyer plus toutes les charges et moins les décharges')
    },
    yesNoAction (a) {
      if(a=='yes') this.publishing()
    },
    setChambres (index) {
      let locataires = this.$store.state.load.locataires
      let locataire = locataires[index]
      let contrats = this.$store.state.load.contrats

      //--- getting corresponding chambre --------------------
      let i, result = [], result1 = [], max = contrats.length
      for(i=0;i<max;i++) {
          if(contrats[i].locataire === locataire.id) {
            result1.push(contrats[i])
            result.push({nom:contrats[i].chambreNom+', cité: ' + contrats[i].citeNom})
          }
      }
      this.chambres = result.length > 0 ? result : [{nom:''}]
      this.contrats = result1.length > 0 ? result1 : [{nom:''}]
      if(result.length > 0) this.setContrat(0)
    },
    setContrat (index) {
      this.contrat = this.contrats[index]
    },
    setDescript (index) {
      this.description = this.chargeElt[index].description
    },
    publishing (){
      if(this.validation()) {
        this.$refs.yAlert.display('Veuillez remplir tous les champs correctement!')
        return
      }
      this.loading = true
      const axios = require('axios')
      axios.post(this.$store.state.baseUrl + 'charge.php', {
        type: 'add',
        manager: this.$store.state.login.id,
        managerName:this.$store.state.login.login,
        contrat: this.contrat.id,
        amount: this.$refs.charge.current.nom =='Charge' ? -this.amount:this.amount,
        reason:this.reason,
        date: this.chargeDate
      })
      .then(({data}) => {
          this.id = data
          this.loadData()
        })
        .catch((error) => {
          console.log(error)
        })
    },
    loadData () {
      const axios = require('axios')
      axios.post(this.$store.state.baseUrl + 'update.php', {
        type:'get'
      })
      .then(({data}) => {
        this.$store.commit('loadCommit',data)
        this.loading = false
        this.$refs.yAlertS.display('Fait!')
      })
      .catch((error) => {
        console.log(error)
      })
    }
  },
  mounted () {
    this.setChambres(0)
    this.setDescript(0)
  }
}
