import LLoading from '@/components/lLoading/LLoading'
import News from '../global/news/News'
//import { Filesystem, Directory, Encoding } from '@capacitor/filesystem'
export default {
    name: 'start',
    components: {
      LLoading,
      News
    },
    data () {
      return {
        loading: false,
        newsDisp:false,
        news:''
      }
    },
    methods: {
      loadNews () {
          this.loading = true
          const axios = require('axios')
          axios.post(this.$store.state.baseUrl + 'news.php',{})
          .then(({data}) => {
              this.loading = false
              if(data != '0') {
                this.newsDisp = true
                this.news = data
              }
              else this.$router.push('/connexion')
          })
          .catch((error) => {
              console.log(error)
          })
        }
  }
}