const store = createStore({
  state () {
    return {
      //baseUrl: 'https://dembele7.000webhostapp.com/location_backend/',
      //baseUrl: 'http://blwcasino.epizy.com/location_backend/',
      //baseUrl: 'https://blwcasino.000webhostapp.com/location_backend/',
      //baseUrl: 'http://location.westus3.cloudapp.azure.com/location_backend/',
      //baseUrl: 'https://icy-meadow-45f90fb3f4044db5a39a3a5d36cb3b4e.azurewebsites.net/location_backend/',
      //baseUrl: 'http://192.168.43.251/projet/location_backend/',
      //baseUrl: 'http://localhost/projet/location_backend/',
      //baseUrl: 'http://localhost:3000/',
      //baseUrl: 'http://192.168.43.251:3000/',
      baseUrl: 'https://renting.sassayer.com/',
      // baseUrl: 'https://ydelocation.com/',
      mois: ['Janvier','Février','Mars','Avril','Mai','Juin','Juillet',
      'Aout','Septembre','Octobre','Novembre','Décembre'],
      load: {
        loading: true,
        loaded: false,
        cites:[{nom:''}],
        chambres:[{nom:''}],
        locataires:[{nom:''}],
        contrats:[],
        payments:[],
        charges:[],
        variants:[],
        serverDate:''
      },
      login: {
        role: -1,
        login: '',
        tel:'',
        id:''
      }
    }
  },
  mutations: {
    loading(state,playload) {
      state.load.loading = playload
    },
    setLoaded(state,playload) {
      state.load.loaded = playload
    },
    loadCommit(state,playload) {
      state.load.cites = playload.cites
      state.load.chambres = playload.chambres
      state.load.contrats = playload.contrats
      state.load.payments = playload.payments
      state.load.locataires = playload.locataires
      state.load.charges = playload.charges
      state.load.variants = playload.variants
      state.load.serverDate = playload.serverDate
    },
    citeCommit (state,playload) {
      state.load.cites = playload
    },
    chambreCommit (state,playload) {
      state.load.chambres = playload
    },
    locataireCommit (state,playload) {
      state.load.locataires = playload
    },
    contratCommit (state,playload) {
      state.load.contrats = playload
    },
    paymentCommit (state,playload) {
      state.load.payments = playload
    },
    loginCommit (state,playload) {
      state.login.login = playload.login
      state.login.id = playload.id
      state.login.role = playload.role
    }
  }
})
const app = createApp(App1)
app.use(router)
app.use(store)
//app.use(VueCordova)
app.mount('#app')
router.beforeEach(middleware({store}))

App.addListener('backButton', ({ canGoBack }) => {
  if(canGoBack){
    window.history.back();
  } else {
    App.exitApp();
  }
});

