export default {
    name: 'chambre',
    data () {
        return {
            Display: false,
            chambre: [],
            cite: [],
            contrat: [{locataireNom:'Aucun'}]
        }
    },
    methods: {
        goBack() {
            //this.$router.go(-1)
            this.Display = false
        },
        toLocataire (locId) {
            this.$router.push({name: 'locataire', params: {locId:locId}})
        },
        display(cham) {
            this.chambre = cham
            this.getCite()
            this.contrat = this.getContrat(this.chambre.id)
            this.Display = true
        },
        getCite () {
            let cites = this.$store.state.load.cites

            //--- getting corresponding chambre --------------------
            let i, max = cites.length
            for(i=0;i<max;i++) {
                if(cites[i].id === this.chambre.cite) {
                    this.cite = cites[i]
                    break
                }
            }
        },
        getContrat (chambreId) {
            let contrats = this.$store.state.load.contrats

            //--- getting corresponding chambre --------------------
            let i, max = contrats.length
            for(i=0;i<max;i++) {
                if(contrats[i].chambre === chambreId) {
                    return contrats[i]
                }
            }
            return ''
        }
    }
}