import ChambForm from '@/components/forms/ChambForm'
import EditMenu from '@/components/global/editMenu/EditMenu'
import YAlert from '@/components/global/yAlert/YAlert'
import LLoading1 from '@/components/global/lLoading1/LLoading1'
import YesNo from '@/components/global/yesNo/YesNo'
import Chambre from '@/components/chambre/Chambre'
export default {
    name: 'chambres',
    components: {
        ChambForm,
        EditMenu,
        YAlert,
        LLoading1,
        YesNo,
        Chambre
    },
    data () {
        return {
            loading: false,
            chambres: [{id:1,nom:'chambre A',location:'derrierre boutique',description:'3 pieces',loyerParDefaut:222222},
            {id:1,nom:'chambre Dagobergo',location:'derrierre boutique',description:'3 pieces'},
            {id:1,nom:'chamb',location:'derrierre boutique',description:'3 pieces'}
            ],
            current: [],
            menu: ['Modifier','Supprimer'],
            cite: [] // the data of the current cite
        }
    },
    methods: {
        select (option) {
            this.$refs.menu.Display = false
            if(option === 'Modifier') {
                this.showForm('modify')
            }
            else if(option === 'Supprimer') {
                this.$refs.yesNo.display(this.current.nom)
            }
        },
        showMenu (elt) {
            if(this.$refs.menu.Display === false){
                this.$refs.menu.Display = true
                this.current = elt
            }
        },
        showForm(type) {
            this.$refs.form.display(type)
        },
        yesNoAction (a) {
            if(a=='yes') this.delete()
        },
        delete () {
            this.loading = true
            const axios = require('axios')
            axios.post(this.$store.state.baseUrl + 'chambres.php', {
                type:'delete',
                id:this.current.id
            })
            .then(() => {
                this.loadData()
            })
            .catch((error) => {
                this.loading = false
                console.log(error)
            })
        },
        loadData () {
            const axios = require('axios')
            axios.post(this.$store.state.baseUrl + 'update.php', {
              type:'get'
            })
            .then(({data}) => {
              this.$store.commit('loadCommit',data)
              this.loading = false
              this.$refs.yAlertS.display('Fait !')
            })
            .catch((error) => {
              console.log(error)
            })
        },
        setColor (a) {
            return a == 0 ? 'rgb(10,244,10)' : 'rgb(244,10,10)'
        },
        setChambres () {
            let index = this.$route.params.citeKey
            let cites = this.$store.state.load.cites
            this.cite = cites[index]
            let chambres = this.$store.state.load.chambres

            //--- getting corresponding chambre --------------------
            let i, result = [], max = chambres.length
            for(i=0;i<max;i++) {
                if(chambres[i].cite === this.cite.id) {
                    result.push(chambres[i])
                }
            }
            this.chambres = result
        },
        toChambre (cham) {
            this.$refs.chambre.display(cham)
        },
        getLocataire (chambreId) {
            let result = this.getContrat(chambreId)
            return result.locataireNom
        },
        getContrat (chambreId) {
            let contrats = this.$store.state.load.contrats

            //--- getting corresponding chambre --------------------
            let i, max = contrats.length
            for(i=0;i<max;i++) {
                if(contrats[i].chambre === chambreId) {
                    return contrats[i]
                }
            }
            return {locataireNom:'aucun'}
        }
    },
    watch: {
        '$store.state.load.chambres': function(){
            this.setChambres()
        }
    },
    mounted () {
        this.setChambres()
    }
}