import LDropDown from '@/components/forms/lDropDown/LDropDown.vue'
import LLoading1 from '@/components/global/lLoading1/LLoading1'
import YAlert from '@/components/global/yAlert/YAlert'
import ImageUploader from 'vue-image-upload-resize/src/components/ImageUploader'
export default {
  name: 'chambForm',
  components: {
    LDropDown,
    LLoading1,
    YAlert,
    ImageUploader
  },
  props: {
    info: Object,
    cite:{
      type:Array,
      default:[{nom:''}]
    }
  },
  data () {
    return {
      type: 'add',
      Display: false,
      loading: false,
      nom: '',
      location: '',
      loyerParDefaut:null,
      description: ''
    }
  },
  computed: {
    title () {
      return this.type === 'add' ? 'Ajouter une chambre' : 'Modifier chambre '+ this.info.nom
    }
  },
  methods: {
    display(type) {
      if(type === 'modify'){
        this.nom = this.info.nom
        this.location = this.info.location
        this.loyerParDefaut = this.info.loyerParDefaut
        this.description = this.info.description
      }
      this.type = type
      this.Display = true
    },
    close () {
      this.$refs.yAlertS.Display = false
      this.$refs.yAlert.Display = false
      this.nom = ''
      this.location = ''
      this.loyerParDefaut = 10000
      this.description = ''
      this.Display = false
    },
    publish () {
      if(this.type === 'add') this.addChambre()
      else this.modifyChambre()
    },
    validation () {
      return this.nom === '' || typeof this.$refs.cite.current === 'undefined'
    },
    addChambre (){
      if(this.validation()) {
        this.$refs.yAlert.display('Choissisez au moins un nom et une cité!')
        return
      }
      this.loading = true
      const axios = require('axios')
      axios.post(this.$store.state.baseUrl + 'chambres.php', {
        type: 'add',
        nom:this.nom,
        location:this.location,
        cite: this.$refs.cite.current.id,
        loyerParDefaut:this.loyerParDefaut,
        description:this.description
      })
        .then((response) => {
          if(response.data === 'exist') {
            this.loading = false
            this.$refs.yAlert.display('Le nom est déja utilisé!')
          }
          else {
            this.loadData()
          }
        })
        .catch((error) => {
          console.log(error)
          this.loading = false
        })
    },
    modifyChambre (){
      if(this.validation()) {
        this.$refs.yAlert.display('Veuillez entrer au moins le nom!')
        return
      }
      this.loading = true
      const axios = require('axios')
      axios.post(this.$store.state.baseUrl + 'chambres.php', {
        type: 'modify',
        id: this.info.id,
        nom:this.nom,
        location:this.location,
        loyerParDefaut:this.loyerParDefaut,
        description:this.description
      })
        .then((response) => {
          if(response.data === 'exist') {
            this.loading = false
            this.$refs.yAlert.display('Le nom est déja utilisé!')
          }
          else {
            this.loadData()
          }
        })
        .catch((error) => {
          console.log(error)
          this.loading = false
        })
    },
    loadData () {
      const axios = require('axios')
      axios.post(this.$store.state.baseUrl + 'update.php', {
        type:'get'
      })
      .then(({data}) => {
        this.$store.commit('loadCommit',data)
        this.loading = false
        this.$refs.yAlertS.display('Enregistré!')
      })
      .catch((error) => {
        console.log(error)
      })
    }
  }
}
