import CiteForm from '@/components/forms/CiteForm'
import EditMenu from '@/components/global/editMenu/EditMenu'
import CiteInfo from '@/components/global/editMenu/CiteInfo'
import YAlert from '@/components/global/yAlert/YAlert'
import LLoading1 from '@/components/global/lLoading1/LLoading1'
import YesNo from '@/components/global/yesNo/YesNo'
export default {
    name: 'cites',
    components: {
        CiteForm,
        EditMenu,
        YAlert,
        LLoading1,
        YesNo,
        CiteInfo
    },
    data () {
        return {
            loading: false,
            cites:this.$store.state.load.cites,//[{id:1,nom:'CITE A',location:'derrierre boutique',description:'3 pieces'}],
            current: [],
            menu: ['Modifier','Supprimer','Infos']
        }
    },
    methods: {
        select (option) {
            this.$refs.menu.Display = false
            if(option === 'Modifier') {
                this.showForm('modify')
            }
            else if(option === 'Supprimer') {
                this.$refs.yesNo.display(this.current.nom)
            }
            else if(option === 'Infos') {
                this.$refs.citeInfo.display(this.current)
            }
        },
        showMenu (elt) {
            if( this.$refs.menu.Display === false){
                this.$refs.menu.Display = true
                this.current = elt
            }
        },
        showForm(type) {
            this.$refs.form.display(type)
        },
        yesNoAction (a) {
            if(a=='yes') this.delete()
        },
        delete () {
            this.loading = true
            const axios = require('axios')
            axios.post(this.$store.state.baseUrl + 'cites.php', {
                type:'delete',
                id:this.current.id
            })
            .then(() => {
                this.loadData()
            })
            .catch((error) => {
                console.log(error)
            })
        },
        loadData () {
            const axios = require('axios')
            axios.post(this.$store.state.baseUrl + 'update.php', {
              type:'get'
            })
            .then(({data}) => {
              this.$store.commit('loadCommit',data)
              this.loading = false
              this.$refs.yAlertS.display('Fait !')
            })
            .catch((error) => {
              console.log(error)
            })
        },
        toChambres (citeKey) {
            this.$router.push({name: 'chambres', params: {citeKey:citeKey}})
        },
    },
    watch: {
        '$store.state.load.cites'() {
            this.cites = this.$store.state.load.cites
        }
    },
    mounted () {
        this.cites = this.$store.state.load.cites
    }
}