import EditMenu from '@/components/global/editMenu/EditMenu'
import YesNo from '@/components/global/yesNo/YesNo1'
import YAlert from '@/components/global/yAlert/YAlert'
import ModifyLocataire from '@/components/forms/ModifyLocataire'
import LLoading1 from '@/components/global/lLoading1/LLoading1'
export default {
    name: 'locataire',
    components: {
        EditMenu,
        YesNo,
        YAlert,
        ModifyLocataire,
        LLoading1
    },
    data () {
        return {
            loading: false,
            menu: ['Modifier','Supprimer', 'Arreter'],
            locataire: [],
            contrats: [],
            ctr:[],
            variants:[],
            expirations: {}
        }
    },
    methods: {
        balanceColor (number) {
            //return number >= 0 ? 'rgb(25, 112, 25)' : 'rgb(235, 27, 61)'
            return number >= 0 ? 'black' : 'black'
        },
        setLocataire () {
            let locataires = this.$store.state.load.locataires
            let i, id = this.$route.params.locId, max = locataires.length
            for(i=0; i<max; i++) {
                if(locataires[i].id == id) {
                    this.locataire = locataires[i]
                    break
                }
            }
            this.setContrat()
            this.setVariant()
        },
        setContrat () {
            let contrats = this.$store.state.load.contrats
            let i, result = [], max = contrats.length
            for(i=0; i<max; i++) {
                if(contrats[i].locataire === this.locataire.id) {
                    result.push(contrats[i])
                }
            }
            //--- set variant
            this.contrats = result
            for(i=0;i<result.length;i++) {
                this.setVariant(result[i].id,i)
                this.setBalance(result[i])
                this.expiration(result[i],i)
            }
        },
        setVariant (contrat,index) {
            let variants = this.$store.state.load.variants
            let i, result = [], max = variants.length
            for(i=0; i<max; i++) {
                if(variants[i].contrat === contrat ) {
                    result.push(variants[i])
                }
            }
            this.contrats[index] = {...this.contrats[index],variants:result}
        },
        select (option) {
            this.$refs.menu.Display = false
            if(option === 'Modifier') {
                this.$refs.modifier.display(this.ctr)
            }
            else if(option === 'Supprimer') {
                this.$refs.yesNo.display('Voulez vous vraiment éffectuer cette opération ? C\'est une opération critique')
            }
            else if(option === 'Arreter') {
                this.$refs.yesNo1.display('Voulez vous vraiment arreter le contrat ? C\'est une opération critique')
            }
        },
        showMenu (elt) {
            if( this.$refs.menu.Display === false){
                this.$refs.menu.Display = true
                this.ctr = elt
            }
        },
        yesNoAction (a) {
            if(a=='yes') this.delete()
        },
        yesNoAction1 (a) {
            if(a=='yes') this.stopContrat()
        },
        delete () {
            this.loading = true
            const axios = require('axios')
            axios.post(this.$store.state.baseUrl + 'contrat.php', {
                id: this.ctr.id,
                type:'delete'
            })
            .then(({data}) => {
                console.log(data)
                this.loadData()
            })
            .catch((error) => {
                console.log(error)
                this.loading = false
            })
        },
        stopContrat () {
            this.loading = true
            const axios = require('axios')
            axios.post(this.$store.state.baseUrl + 'contrat.php', {
                id: this.ctr.id,
                type:'stop'
            })
            .then(({data}) => {
                console.log(data)
                this.loadData()
            })
            .catch((error) => {
                console.log(error)
                this.loading = false
            })
        },
        loadData () {
            const axios = require('axios')
            axios.post(this.$store.state.baseUrl + 'update.php', {
              type:'get'
            })
            .then(({data}) => {
              this.$store.commit('loadCommit',data)
              this.loading = false
              this.$refs.yAlertS.display('Fait!')
            })
            .catch((error) => {
              console.log(error)
            })
        },
        dateFormat (dt) {
            let a = new Date(dt)
            let year = a.getFullYear()
            let month = a.getMonth()+1
            let day = a.getDate();

            month = (month < 10)? '0'+ month : month;
            day= (day < 10)? '0' + day : day;
            return day + ' ' + this.$store.state.mois[month -1] + ' ' + year
        },
        endMonth(dat) {
            let c = new Date(dat.getFullYear(),dat.getMonth()+1,0)
            return c
        },
        nextMonth(d,day) {
            d.setDate(1)
            d.setMonth(d.getMonth()+1)
            let b = this.endMonth(d)
            if (b.getDate()<day) d = b
            else  d.setDate(day)
            return d
        },
        previousMonth(d,day) {
            d.setDate(1)
            d.setMonth(d.getMonth()-1)
            let b = this.endMonth(d)
            if (b.getDate()<day) d = b
            else  d.setDate(day)
            return d
        },
        /*slashFormat (dat) {
            dat = dat.replace('-','/')
            dat = dat.replace('-','/')
            return dat
        },*/
        setBalance (ct) {
            let maxTimeDate = new Date(this.$store.state.load.serverDate)
            maxTimeDate.setHours(23,59,59,0)
            let stopped = new Date(ct.stopped)
            let maxTime = stopped.getTime() < this.maxTime ? stopped.getTime() : maxTimeDate.getTime()
            let db = new Date(ct.date_debut)
            db.setHours(0,0,0,0)
            let day = db.getDate()
            db = ct.payDebutMois == 1 ? this.previousMonth(db,day) : db

            let monthElapsed = 0, c = true
            let a = new Date(db)
            while (c) {

                a = this.nextMonth(a,day)
                if(a.getTime() > maxTime) {
                    c = false
                    break
                }
                else monthElapsed ++
            }

            ct.newBalance = ct.totalPayer - monthElapsed*ct.loyer + ct.totalCharge - this.setTotalVariant(ct,maxTime,db)
        },
        
        setTotalVariant (ct,maxTime,db) { 
            let a = new Date(db)
            let day = a.getDate()
            let totalVariant = 0
            let j = 0, vr = ct.variants, max = ct.variants.length
            for(j=0;j<max;j++) {
                    //---- let set the end of this variant which is the start of the next variant ----

                    let end = j == (max-1) ? new Date('2100-1-1') : new Date(vr[j+1].startMonth)
                    end.setHours(23,59,59,0)

                    if(a.getTime() > maxTime ) break
                    if(j>0) a = this.previousMonth(a,day)
                    let c = true
                    while(c) {
                        
                        //-- a = la date de fin après elapsed mois écoulés ----

                        a = this.nextMonth(a,day)

                        if(a.getTime() > end.getTime() || a.getTime() > maxTime) {
                            c = false
                            break
                        }

                        totalVariant +=vr[j].montant
                    }
            }
            
            return totalVariant
        }
        
        
        ,
        expiration (ct,index) {
            let total = ct.totalPayer + ct.totalCharge
            //let monthPaid = 0
            let toPay = 0
            let db = new Date(ct.date_debut)
            db.setHours(0,0,0,0)

            let day = db.getDate()
            db = ct.payDebutMois == 1 ? this.previousMonth(db,day) : db
            
            let c = true, a = new Date(db), expirationDate = new Date(db)
            expirationDate = this.nextMonth(expirationDate,day)  //---- initialization-----------
            
            while (c) {
                a = this.nextMonth(a,day)
                //console.log('---------'+a)
                //console.log(db+'----------') 
                toPay += ct.loyer + this.setTotalVariant(ct,a.getTime(),db)
                console.log(total)
                if(toPay > total) {
                    c = false
                    break
                }
                else {
                    expirationDate = a
                    //monthPaid ++
                }
            }
            
            // --- let save the expiration date -------------
            this.setExpirations(index,expirationDate)
            
            let year = expirationDate.getFullYear()
            let month = expirationDate.getMonth()
            let jour = expirationDate.getDate()
            jour= (jour < 10)? '0' + jour : jour
            
            ct.expirationDate = jour + ' ' + this.$store.state.mois[month] + ' ' + year
        },
        setExpirations (index,a) {
            if(typeof this.expirations[index] === 'undefined') {
                this.expirations[index] ={expDate:a,index:index, value:0}
                let k = this
                setInterval(function(){
                    let d = new Date()
                    let ms = a.getTime() - d.getTime()
                    if(ms < 0) { // means it expired
                        k.expirations[index].value = '0jrs:0h:0m:0s'
                        return
                    }
                    let r
                    let rest = ms
                    let cd = 24 * 60 * 60 * 1000
                    let days = Math.floor(ms/cd)
                    rest = (rest - days*cd)
                    let hours = Math.floor(rest/(60*60*1000))
                    rest = (rest - hours*60*60*1000)
                    let mins = Math.floor(rest/(60*1000))
                    rest = rest - mins*60*1000
                    let secs = Math.floor(rest/1000)
                    r = days + 'jrs:'+ hours 
                    + 'h:' + mins + 'm:' + (secs<10 ? '0'+secs:secs)+ 's'

                    k.expirations[index].value = r
                }, 1000)
            }
        }
    },
    watch: {
        '$store.state.load.locataires' () {
            this.setLocataire()
        },
        '$store.state.load.contrats' () {
            this.setLocataire()
        },
    },
    mounted () {
        this.setLocataire()
    }
}