import LDropDown from '@/components/forms/lDropDown/LDropDown.vue'
import LLoading1 from '@/components/global/lLoading1/LLoading1'
import YAlert from '@/components/global/yAlert/YAlert'
export default {
  name: 'chambForm',
  components: {
    LDropDown,
    LLoading1,
    YAlert
  },
  data () {
    return {
      loading: false,
      info:[{nom:''}],
      annees: [{nom:2020},{nom:2021},{nom:2022},{nom:2023},{nom:2024},{nom:2025},{nom:2026},{nom:2027},
        {nom:2028},{nom:2029},{nom:2030},{nom:2031},{nom:2032},{nom:2033},{nom:2034},{nom:2035}
      ],
      cites:[{nom:''}],
      mois: [{nom:'Tous',id:-1},{nom:'Janvier',id:0},{nom:'Février',id:1},{nom:'Mars',id:2},
      {nom:'Avril',id:3},{nom:'Mai',id:4},{nom:'Juin',id:5},{nom:'Juillet',id:6},{nom:'Aout',id:7},
      {nom:'Septembre',id:8},{nom:'Octobre',id:9},{nom:'Novembre',id:10},{nom:'Décembre',id:11}],
      current:''
    }
  },
  methods: {
    close () {
      this.$router.go(-1)
    },
    toRecap () {
      let annee = this.$refs.annee.current.nom
      let mois = this.$refs.mois.current.nom
      let moisId = this.$refs.mois.current.id
      let cite = this.$refs.cite.current.nom
      let citeId = this.$refs.cite.current.id
      this.$router.push({name: 'recap', params: {annee:annee,mois:mois,cite:cite,citeId:citeId,moisId:moisId}})
    }
  },
  mounted () {
    this.cites = [...this.$store.state.load.cites]
    this.cites.unshift({nom:'Toutes',id:'-1'})
    let d = new Date()
    let m = d.getMonth()
    let y = d.getFullYear()
    this.$refs.annee.selecting(y-2020)
    this.$refs.mois.selecting(m+1)
  }
}
