import EditMenu from '@/components/global/editMenu/EditMenu'
import LocatFImg from '@/components/forms/LocatFImg'
import YAlert from '@/components/global/yAlert/YAlert'
import LLoading1 from '@/components/global/lLoading1/LLoading1'
import YesNo from '@/components/global/yesNo/YesNo'
export default {
    name: 'locataires',
    components: {
        EditMenu,
        LocatFImg,
        YAlert,
        LLoading1,
        YesNo
    },
    data () {
        return {
            loading: false,
            /* [{id: 1, nom:'JEAN JACQUES',chambre:'Chambre 2',cite:'Cite A'},
                    {id: 1, nom:'OSCAR LUIZ',chambre:'Chambre 3',cite:'Cite A'},
                    {id: 1, nom:'FRANK KESSIE',chambre:'Chambre 4',cite:'Cite A'},
            ],*/
            current: [],
            menu: ['Modifier','supprimer'],
            locataires: this.$store.state.load.locataires,
            bUrl:this.$store.state.baseUrl
        }
    },
    methods: {
        select (option) {
            this.$refs.menu.Display = false
            if(option === 'Modifier') {
                this.$refs.LocatFImg.Display = true
            }
            else if(option === 'supprimer') {
                this.$refs.yesNo.display(this.current.nom)
            }
        },
        showMenu (elt) {
            if( this.$refs.menu.Display === false){
                this.$refs.menu.Display = true
                this.current = elt
            }
        },
        yesNoAction (a) {
            if(a=='yes') this.delete()
        },
        delete () {
            this.loading = true
            const axios = require('axios')
            axios.post(this.$store.state.baseUrl + 'locataire.php', {
                type:'delete',
                id:this.current.id
            })
            .then(() => {
                this.loadData()
            })
            .catch((error) => {
                this.loading = false
                console.log(error)
            })
        },
        loadData () {
            const axios = require('axios')
            axios.post(this.$store.state.baseUrl + 'update.php', {
              type:'get'
            })
            .then(({data}) => {
              this.$store.commit('loadCommit',data)
              this.loading = false
              this.$refs.yAlertS.display('Fait!')
            })
            .catch((error) => {
              console.log(error)
            })
        },
        toLocataire (locId) {
            this.$router.push({name: 'locataire', params: {locId:locId}})
        },
        getDebt (id) {
            let contrats = this.$store.state.load.contrats
            let i, max = contrats.length
            for(i=0;i<max;i++) {
                if(contrats[i].locataire == id && contrats[i].balance < 0 ) {
                    return 'rgb(255, 0, 76)'/*'rgb(255, 0, 0,0.1).locatElName + span {
                        color: rgb(206, 10, 3);
                    }*/ //true
                }
            }
            return '#232323'//false
        },
        getLocContrat (locId) {
            let i, ct = this.$store.state.load.contrats, max = this.$store.state.load.contrats.length
            for(i=0;i<max;i++) {
                if(ct[i].locataire == locId) {
                    return {chambreNom:ct[i].chambreNom,citeNom:ct[i].citeNom}
                }
            }
        },
        getLocataire () {
            let result = []
            let i, l = this.$store.state.load.locataires, max = this.$store.state.load.locataires.length
            for(i=0;i<max;i++) {
                result.push({ ...l[i], ...this.getLocContrat(l[i].id) })
            }
            this.locataires = result
        }
    },
    watch: {
        "$store.state.load.locataires"() {
            this.getLocataire()
        }
    },
    mounted () {
        this.getLocataire()
    }
}