import InputI0 from '@/components/inscription/InputI0.vue'
import ButtonI0 from '@/components/inscription/ButtonI0.vue'
import ButtonI1 from '@/components/inscription/ButtonI1.vue'
import DropDownList from '@/components/inscription/DropDownList.vue'
import LLoading1 from '@/components/global/lLoading1/LLoading1'
export default {
  name: 'inscription',
  components: {
    InputI0,
    ButtonI0,
    ButtonI1,
    DropDownList,
    LLoading1
  },
  data () {
    return {
      loading:false
    }
  },
  methods: {
    submiting (e) {
      e.preventDefault()
      return false
    },
    save () {
      if (!this.validation()) return
      
      this.$store.commit('setLoaded',false) // Allow to update before loading menu page
      this.loading = true
      const axios = require('axios')
      axios.post(this.$store.state.baseUrl + 'inscription.php', {
        type:'add',
        name: this.$refs.surname.message,
        tel: this.$refs.tel.message,
        password: this.$refs.password.message,
        confirmpw: this.$refs.confirmpw.message
      })
        .then(({data}) => {
          console.log(data)
          this.loading = false
          if(data == 'exist') alert('nom ou téléphone déjà prix déjà prix')
          else {
            alert('Compte créé avec success !')
            this.$router.push('/connexion')
          }
        })
        .catch((error) => {
          console.log(error)
        })
    },
    validation () {
      let regex1 = /^.{2,22}$/
      if (this.$refs.surname.message == '' ||
        !regex1.test(this.$refs.tel.message)) {
        alert('nom ou téléphone incorrect')
        return false
      }
      if (this.$refs.password.message == '') {
        alert('password incorrect')
        return false
      }
      if (this.$refs.password.message !== this.$refs.confirmpw.message) {
        alert('passwords did not match')
        return false
      }
      return true
    }
  }
}
