import LDropDown from '@/components/forms/lDropDown/LDropDown.vue'
import LLoading1 from '@/components/global/lLoading1/LLoading1'
import YAlert from '@/components/global/yAlert/YAlert'
import ImageUploader from 'vue-image-upload-resize/src/components/ImageUploader'
export default {
  name: 'locatFImg',
  components: {
    LDropDown,
    LLoading1,
    YAlert,
    ImageUploader
  },
  props: {
    info: Object
  },
  data () {
    return {
      Display: false,
      file: '',
      loading: false,
      imageName: '',
      nom:'',
      prenom:'',
      tel:''
    }
  },
  methods: {
    close () {
      this.Display = false
      document.getElementById('lPbblah1').src = ''
      this.file = ''
    },
    preview (file) {
      // const [file] = this.$refs.image.
      if (file) {
        this.file = file
        document.getElementById('lPbblah1').src = URL.createObjectURL(file)
      }
    },
    sendImage () {
      this.loading = true
      const axios = require('axios')
      var formData = new FormData()
      if (this.file) {
        formData.append('image_name', this.file)
        axios.post(this.$store.state.baseUrl + 'saveLocImg.php', formData, {
          headers: {
            'Content-Type': 'multipart/form-data'
          }
        })
          .then((response) => {
            if (response.data === 'Error') {
              this.loading = false
              this.$refs.yAlert.display(response.data)
            } else {
              this.imageName = response.data
              this.publishing()
            }
          })
          .catch((error) => {
            console.log(error)
          })
      } 
      else {
        this.publishing()
        //this.$refs.yAlert.display('Pas d\'image')  
      }
    },
    publishing (){
      const axios = require('axios')
      axios.post(this.$store.state.baseUrl + 'locataire.php', {
        type:'modify',
        id:this.info.id,
        image: this.imageName,
        nom: this.nom,
        prenom:this.prenom,
        tel:this.tel
      })
        .then((response) => {
          console.log(response.data)
          this.loadData()
        })
        .catch((error) => {
          console.log(error)
        })
    },
    loadData () {
      const axios = require('axios')
      axios.post(this.$store.state.baseUrl + 'update.php', {
        type:'get'
      })
      .then(({data}) => {
        this.$store.commit('loadCommit',data)
        this.loading = false
        this.$refs.yAlertS.display('Enregistré!')
      })
      .catch((error) => {
        console.log(error)
      })
    }
  },
  watch: {
    info (val) {
      this.nom = val.nom
      this.prenom = val.prenom
      this.tel = val.tel1
      this.imageName = val.image
    }
  }
}
