import html2pdf from 'html2pdf.js'
import LLoading1 from '@/components/global/lLoading1/LLoading1'
import { Filesystem, Directory, /*Encoding*/ } from '@capacitor/filesystem';
export default {
    name: 'facture',
    components: {
        LLoading1
    },
    data () {
        return {
            payment: [],
            totalPaid:0,
            contrat: [],
            dette: 0,
            loading: false,
        }
    },
    methods: {
        print () {
            this.loading = true
            let element = document.getElementById('toPrint')
            var opt = {
              margin:       1,
              filename:     'myfile.pdf',
              image:        { type: 'jpeg', quality: 0.98 },
              html2canvas:  { scale: 2, width:1366 , windowWidth:1366 },
              jsPDF:        { unit: 'in', format: 'a4', orientation: 'portrait' }
            }
            let t = this
            /*html2pdf().from(element).set(opt).output('datauristring').then((output) => {
                this.$refs.lien.href = output
                this.$refs.lien.click()
            })*/
            //html2pdf().from(element).set(opt).save()
            html2pdf().from(element).set(opt).toPdf().output('blob').then(function (data) {
                // The PDF has been converted to a Data URI string and passed to this function.
                // Use pdfAsString however you like (send as email, etc)! For instance:
                //alert(data)
                var reader = new FileReader()
                let k = t
                reader.readAsDataURL(data)
                reader.onloadend = function() {
                    var base64data = reader.result             
                    //alert(base64data)
                    Filesystem.writeFile({
                        path: 'Factures/facture'+ (new Date().getTime())+'.pdf',
                        data: base64data,
                        directory: Directory.Documents,
                        /*encoding: Encoding.UTF8,*/
                        recursive: true
                    }).then((/*response*/) => {
                            k.loading = false
                            alert('Sauvegardé dans Factures')
                        })
                        .catch((error) => {
                            k.loading = false
                            alert(error)
                    })
                }
                //let fileURL = URL.createObjectURL(data)
                //alert(fileURL)
                //let blob = this.dataURItoBlob(pdfAsString)
            });
        },
        nextMonth(d,day) {
            d.setDate(1)
            d.setMonth(d.getMonth()+1)
            let b = this.endMonth(d)
            if (b.getDate()<day) d = b
            else  d.setDate(day)
            return d
        },
        previousMonth(d,day) {
            d.setDate(1)
            d.setMonth(d.getMonth()-1)
            let b = this.endMonth(d)
            if (b.getDate()<day) d = b
            else  d.setDate(day)
            return d
        },
        endMonth(dat) {
            let c = new Date(dat.getFullYear(),dat.getMonth()+1,0)
            return c
        },
        totalPayer () {
            let payId = this.$route.params.payId
            let ctId = this.$route.params.ctId
            let result = 0
            let i, payment = this.$store.state.load.payments, max = payment.length-1
            for(i=max;i >= 0;i--) {

                if(payment[i].contratLocation == ctId && payment[i].id <= payId) {

                    result +=payment[i].montant
                }
                if(payment[i].id == payId) {
                    this.payment = payment[i]
                    this.totalPaid = result
                    break
                }
            }
        },
        setContrat () {
            let contrats = this.$store.state.load.contrats
            let i, id = this.payment.contratLocation, max = contrats.length
            for(i=0; i<max; i++) {
                if(contrats[i].id == id) {
                    this.contrat = contrats[i]
                    break
                }
            }

            let maxTime = new Date (this.payment.date)
            maxTime.setHours(23,59,59)

            this.setVariant(id)
            this.setBalance(this.contrat,maxTime)
            this.expiration(this.contrat)
        },
        setBalance (ct,maxTime) {
            let db = new Date(ct.date_debut)
            db.setHours(0,0,0,0)
            let day = db.getDate()
            db = ct.payDebutMois == 1 ? this.previousMonth(db,day) : db

            let monthElapsed = 0, c = true
            let a = new Date(db)
            while (c) {

                a = this.nextMonth(a,day)
                if(a.getTime() > maxTime) {
                    c = false
                    break
                }
                else monthElapsed ++
            }

            ct.newBalance = this.totalPaid - monthElapsed*ct.loyer - this.setTotalVariant(ct,maxTime,db)
        },
        setTotalVariant (ct,maxTime,db) { 
            let a = new Date(db)
            let day = a.getDate()
            let totalVariant = 0
            let j = 0, vr = ct.variants, max = ct.variants.length
            for(j=0;j<max;j++) {
                    //---- let set the end of this variant which is the start of the next variant ----

                    let end = j == (max-1) ? new Date('2100-1-1') : new Date(vr[j+1].startMonth)
                    end.setHours(23,59,59,0)

                    if(a.getTime() > maxTime ) break
                    if(j>0) a = this.previousMonth(a,day)
                    let c = true
                    while(c) {
                        
                        //-- a = la date de fin après elapsed mois écoulés ----

                        a = this.nextMonth(a,day)

                        if(a.getTime() > end.getTime() || a.getTime() > maxTime) {
                            c = false
                            break
                        }

                        totalVariant +=vr[j].montant
                    }
            }
            
            return totalVariant
        },
        expiration (ct) {
            let total = this.totalPaid
            //let monthPaid = 0
            let toPay = 0
            let db = new Date(ct.date_debut)
            db.setHours(0,0,0)

            let day = db.getDate()
            db = ct.payDebutMois == 1 ? this.previousMonth(db,day) : db
            
            let c = true, a = new Date(db), expirationDate = new Date(db)
            expirationDate = this.nextMonth(expirationDate,day)  //---- initialization-----------
            
            while (c) {
                a = this.nextMonth(a,day)
                //console.log('---------'+a)
                //console.log(db+'----------') 
                toPay += ct.loyer + this.setTotalVariant(ct,a.getTime(),db)
                console.log(total)
                if(toPay > total) {
                    c = false
                    break
                }
                else {
                    expirationDate = a
                    //monthPaid ++
                }
            }
            
            // --- let save the expiration date -------------
            
            let year = expirationDate.getFullYear()
            let month = expirationDate.getMonth()
            let jour = expirationDate.getDate()
            jour= (jour < 10)? '0' + jour : jour
            
            ct.expirationDate = jour + ' ' + this.$store.state.mois[month] + ' ' + year
        },
        setVariant (contrat) {
            let variants = this.$store.state.load.variants
            let i, result = [], max = variants.length
            for(i=0; i<max; i++) {
                if(variants[i].contrat === contrat ) {
                    result.push(variants[i])
                }
            }
            this.contrat = {...this.contrat, variants:result}
        },
        exit () {
            if(this.$route.params.via == 'recap') this.$router.go(-1)
            else this.$router.push('/menu')
        }
    },
    mounted () {
        //let n = new Date(this.$store.state.load.serverDate)
        this.totalPayer()
        this.setContrat()
        this.dette = this.expiration(this.contrat)
    }
}