<template>
<section class="lPblicationForm" >
    <div  style="z-index:3" class="lPBFCTitle">
        <div class="lPBFCCross" @click="$router.go(-1)"><img src="@/assets/Retour.png"/></div>
        Effectuer Un Payement
    </div>
    <div class="lPBFCBody">
            <div class="lPBFCBlock">
                <div class="lPBFCinput">
                    <span>Locataire</span>
                    <LDropDown @selected='setChambres' style="z-index:2;" ref='locataire' :elements="locataires" maxHeight="110px" maxWidth="100%" />
                </div>
                <div class="lPBFCinput">
                    <span>Chambre</span>
                    <LDropDown @selected='setPrice' style="z-index:0;" ref='chambre' :elements="chambres" maxHeight="110px" maxWidth="100%" />
                </div>
                <div class="lPBFCinput">
                    <span style="font-weight:normal">Prix/mois</span><p>{{ contrat.loyer }}FCFA</p>
                </div>
                <div class="lPBFCinput">
                    <span style="font-weight:normal">Dette+solde</span> <p>{{ setBalance(contrat) }}FCFA</p>
                </div>
                <div class="lPBFCinput">
                    <span style="font-weight:normal"> Nouvelle dette+solde</span><p> {{ newBalance }}FCFA</p>
                </div>
                <div class="lPBFCinput">
                    <span style="font-weight:normal">Montant verser</span>
                    <input id="payToPay" class="lPBFCinputStyle" type="number" v-model="payment"/>
                </div>
                <div class="lPBFCinput">
                    <span style="font-weight:normal">date payment</span>
                    <input class="lPBFCinputStyle" type="date" v-model="payDate"/>
                </div>
            </div>
            <div class="lPBFCBlock">
                <div class="lPBFCButton" @click='showYesNo()' ><span>ENREGISTRER</span></div>
            </div>
    </div>
    <LLoading1 :visible="loading"/>
    <YAlert ref="yAlert"/>
    <YAlert @closed='toFacture' ref="yAlertS" type="sucess" />
    <YesNo ref="yesNo" @option='yesNoAction'/>
</section>
</template>
<style src="./locatForm.css">
</style>
<script src = "./paymentForm.js">
</script>
