import LDropDown from '@/components/forms/lDropDown/LDropDown.vue'
import LLoading1 from '@/components/global/lLoading1/LLoading1'
import YAlert from '@/components/global/yAlert/YAlert'
import YesNo from '@/components/global/yesNo/YesNo1'
import ImageUploader from 'vue-image-upload-resize/src/components/ImageUploader'
export default {
  name: 'contratForm',
  components: {
    LDropDown,
    LLoading1,
    YAlert,
    YesNo,
    ImageUploader
  },
  data () {
    return {
      loading: false,
      locataires: this.$store.state.load.locataires.length == 0 ? [{nom:''}] :this.$store.state.load.locataires,
      cites: this.$store.state.load.cites.length == 0 ? [{nom:''}]:this.$store.state.load.cites, //[{id:1,nom:'Some chambre'},{id:2,nom:'A chambre'}],
      chambres: [{nom:''}],
      loyer: 0,
      dateDebut: '',
      charges:0,
      descripCharges:'',
      startMonthPaying:false
    }
  },
  methods: {
    validation () {
      let dn = new Date(this.$store.state.load.serverDate)
      let db = new Date(this.dateDebut)
      let test = db.getTime() - dn.getTime()
      let result = this.loyer < 0 || this.loyer === ''  || this.$refs.chambre.current.nom === '' || isNaN(test) /*|| test<0*/
      if(this.charges == '') this.charges = 0
      return result
    },
    showYesNo () {
      this.$refs.yesNo.Display = true
    },
    yesNoAction (a) {
      if(a=='yes') this.publishing()
    },
    setChambres (index) {
      let cites = this.$store.state.load.cites
      let cite = cites[index]
      let chambres = this.$store.state.load.chambres

      //--- getting corresponding chambre --------------------
      let i, result = [], max = chambres.length
      for(i=0;i<max;i++) {
          if(chambres[i].cite === cite.id && chambres[i].occupied == 0) {
              result.push(chambres[i])
          }
      }
      this.chambres = result.length > 0 ? result : [{nom:''}]
      if(result.length > 0) this.setPrice(0)
    },
    setPrice (index) {
      this.loyer = this.chambres[index].loyerParDefaut
    },
    publishing (){
      if(this.validation()) {
        this.$refs.yAlert.display('Veuillez remplir tous les champs!')
        return
      }
      this.loading = true
      const axios = require('axios')
      axios.post(this.$store.state.baseUrl + 'contrat.php', {
        type: 'add',
        locataire: this.$refs.locataire.current.id,
        chambre: this.$refs.chambre.current.id,
        loyer: this.loyer,
        charges: this.charges,
        descripCharges: this.descripCharges,
        startMonthPaying: this.startMonthPaying ? 1 : 0,
        dateDebut: this.dateDebut
      })
        .then((response) => {
          if(response.data === 'error') {
            this.loading = false
            this.$refs.yAlert.display('Erreur chambre occupée!')
          }
          else {
            this.loadData()
          }
        })
        .catch((error) => {
          alert(error)
        })
    },
    loadData () {
      const axios = require('axios')
      axios.post(this.$store.state.baseUrl + 'update.php', {
        type:'get'
      })
      .then(({data}) => {
        this.$store.commit('loadCommit',data)
        this.loading = false
        this.$refs.yAlertS.display('Enregistré!')
      })
      .catch((error) => {
        console.log(error)
      })
    }
  },
  mounted () {
    this.setChambres(0)
  }
}
