import InputI0 from '@/components/inscription/InputI0.vue'
import ButtonI0 from '@/components/inscription/ButtonI0.vue'
import ButtonI1 from '@/components/inscription/ButtonI1.vue'
import DropDownList from '@/components/inscription/DropDownList.vue'
import LLoading1 from '@/components/global/lLoading1/LLoading1'
export default {
  name: 'Connexion',
  components: {
    InputI0,
    ButtonI0,
    ButtonI1,
    DropDownList,
    LLoading1
  },
  data () {
    return {
      loading:false
    }
  },
  methods: {
    submiting (e) {
      e.preventDefault()
      return false
    },
    contactMe1 () {
      alert("Contactez +237 652 95 29 41 ou estadiotamo@gmail.com")
    },
    contactMe () {
      alert("Problème de server, svp veuillez contactez +237 652 95 29 41 ou estadiotamo@gmail.com")
    },
    loggin () {
      if (!this.validation()) return
      this.$store.commit('setLoaded',false) // Allow to update before loading menu page
      this.loading = true
      const axios = require('axios')
      axios.post(this.$store.state.baseUrl + 'inscription.php', {
        type:'log',
        tel: this.$refs.tel.message,
        password: this.$refs.password.message
      })
        .then(({data}) => {
          if(data !== 'nothing') {
            this.$store.commit('loginCommit',data)
            this.$router.push('menu')
          }
          else alert('compt inexistant')
          this.loading = false
        })
        .catch((error) => {
          console.log(error)
        })
    },
    validation () {
      let regex1 = /^.{2,22}$/
      if (!regex1.test(this.$refs.tel.message)) {
        alert('nom ou téléphone incorrect')
        return false
      }
      if (this.$refs.password.message == '') {
        alert('password incorrect')
        return false
      }
      return true
    }
  }
}
