<template>
  <div class="compte">
    <div style="z-index:1" class="lPBFCTitle">
        <div class="lPBFCCross" @click="$router.go(-1);"><img src="@/assets/Retour.png"/></div>
        Compte
    </div>
    <div class="compteBody">
      <div class="compteInfo">
        <p class="compteHead">Administrateur</p>
        <div>Login: <span>{{ $store.state.login.login }}</span></div>
        <div>Tel: <span>{{ $store.state.login.tel }}</span></div>
        <div>Password: <span>******</span></div>
        <div @click="form = !form" style="margin-top:15px" class="compteModif">Modifier</div>
        <div v-show="form" class="compteForm">
          <input v-model="login" type="text" placeholder="nouveau login"/>
          <input v-model="password" type="text" placeholder="nouveau mot de passe"/>
          <input v-model="tel" type="number" placeholder="nouveau téléphone"/>
          <p><span @click="modify">Enregistrer</span><span @click="form = false" >Annuler</span></p>
        </div>
        <div @click="newForm = !newForm" style="margin-top:15px" class="compteModif">Créer nouveau compte</div>
        <div v-show="newForm" class="compteForm">
          <input v-model="newLogin" type="text" placeholder="login"/>
          <input v-model="newPassword" type="text" placeholder="password"/>
          <input v-model="newTel" type="number" placeholder="téléphone"/>
          <div class="compteSelect">
            <span>Type de compte</span>
            <select v-model="newRole" >
              <!-- <option>superuser</option> -->
              <option>superviseur</option>
              <option>secretaire</option>
            </select>
          </div>
          <div v-show="newRole != 'superuser' " class="compteCities">
            <div v-for="(ct,index) in cities" :key="index"  >
              Accès à {{ ct.nom }} <input v-model="ct.chosen" type="checkbox" />
            </div>
          </div>
          <p><span @click="add">Enregistrer</span><span @click="newForm = false" >Annuler</span></p>
        </div>
      </div>
      <div v-for ="(cmt,key,index) in comptes" :key="index" class="compteInfo">
        <p class="compteHead">Autre</p>
        <div>Login: <span>{{ cmt.nom }}</span></div>
        <div>Password: <span>{{ cmt.password }}</span></div>
        <div>Tel: <span>{{ cmt.tel }}</span></div>
        <div>role: <span>{{ cmt.role }}</span></div>
        <div>Accès à: 
            <span v-for="(ct,index1) in cmt.cities" :key="index1">{{ ct.nom }}
              <span v-show="index1 != (cmt.cities.length-1)">,</span> 
            </span>
        </div>
        <div @click="deleteCompte(cmt)" style="margin-top:15px;background-color:rgb(244,50,50);" class="compteModif">Supprimer</div>
      </div>
    </div>
    <LLoading1 :visible="loading"/>
     <YAlert ref="yAlertS" type="success"/>
     <YAlert ref="yAlert"/>
  </div>
</template>
<script src="./compte.js">
</script>
<style src="./compte.css">
</style>