import LDropDown from '@/components/forms/lDropDown/LDropDown.vue'
import LLoading1 from '@/components/global/lLoading1/LLoading1'
import YAlert from '@/components/global/yAlert/YAlert'
import ImageUploader from 'vue-image-upload-resize/src/components/ImageUploader'
export default {
  name: 'locatForm',
  components: {
    LDropDown,
    LLoading1,
    YAlert,
    ImageUploader
  },
  data () {
    return {
      loading: false,
      elements: [{nom:'Célibataire'},{nom:'Marié'},{nom:'En couple'}],
      sexe: [{nom:'Homme'},{nom:'Femme'}],
      nom: '',
      prenom: '',
      tel1: '',
      tel2: '',
      age: '',
      file: '',
      imageName: ''
    }
  },
  methods: {
    displaying () {
      document.getElementById('lPbblah1').src = ''
    },
    preview (file) {
      if (file) {
        this.file = file
        document.getElementById('lPbblah1').src = URL.createObjectURL(file)
      }
    },
    sendImage () {
      if(this.validation()) {
        this.$refs.yAlert.display('veuillez remplir tous les champs importants')
      }
      else {
        //this.loading = true
        const axios = require('axios')
        var formData = new FormData()
        if (this.file) {
          formData.append('image_name', this.file)
          axios.post(this.$store.state.baseUrl + 'saveLocImg.php', formData, {
            headers: {
              'Content-Type': 'multipart/form-data'
            }
          })
            .then((response) => {
              console.log(response.data)
              if (response.data === 'Error') {
                this.loading = false
                this.$refs.yAlert.display(response.data)
              } else {
                this.imageName = response.data
                this.publishing()
              }
            })
            .catch((error) => {
              console.log(error)
            })
        } else { this.publishing() } // s'il n(y a pas de fichier on publi quand meme le reste)
      }
    },
    validation () {
      let result = this.nom === '' || this.prenom === '' || this.tel1 === ''
      return result
    },
    publishing (){
      this.loading = true
      const axios = require('axios')
      axios.post(this.$store.state.baseUrl + 'saveLocataire.php', {
        nom: this.nom,
        prenom: this.prenom,
        tel1: this.tel1,
        tel2: this.tel2,
        age: this.age,
        image: this.imageName,
        sexe: this.$refs.sexe.current.nom,
        situation: this.$refs.situation.current.nom
      })
      .then(({data}) => {
          if(data === 'exist') {
            this.loading = false
            this.$refs.yAlert.display('Le nom est déja utilisé!')
          }
          else {
            this.loadData()
          }
        })
        .catch((error) => {
          alert(error)
        })
    },
    loadData () {
      const axios = require('axios')
      axios.post(this.$store.state.baseUrl + 'update.php', {
        type:'get'
      })
      .then(({data}) => {
        this.$store.commit('loadCommit',data)
        this.loading = false
        this.$refs.yAlertS.display('Enregistré!')
      })
      .catch((error) => {
        console.log(error)
      })
    }
  }
}
