import { /*createWebHashHistory*/createWebHistory, createRouter } from 'vue-router'
import LandingPage from '@/components/landingPage/LandingPage'
import Locataires from '@/components/locataires/Locataires'
import Locataire from '@/components/locataire/Locataire'
import Cites from '@/components/cites/Cites'
import Chambres from '@/components/chambres/Chambres'
import Welcome from '@/components/welcome/Welcome'
import Login from '@/components/login/Login'
import LocatForm from '@/components/forms/LocatForm'
import ContratForm from '@/components/forms/ContratForm'
import PaymentForm from '@/components/forms/PaymentForm'
import ChargeForm from '@/components/forms/ChargeForm'
import RecapForm from '@/components/forms/RecapForm'
import Compte from '@/components/compte/Compte'
import Facture from '@/components/facture/Facture'
import Payments from '@/components/payments/Payments'
import Charge from '@/components/charge/Charge'
import Recap from '@/components/recap/Recap'
import Help from '@/components/help/Help'
import Support from '@/components/support/Support'
import StartPage from '@/components/startPage/StartPage'
import Inscription from '@/components/inscription/Inscription'
import Connexion from '@/components/inscription/Connexion'
import loading from '@/middleware/loading'
import isLoaded from '@/middleware/isLoaded'

const routes = [
  {
    path: '/',
    name: 'start',
    component: StartPage,
  },
  {
    path: '/inscription',
    name: 'inscription',
    component: Inscription,
  },
  {
    path: '/connexion',
    name: 'connexion',
    component: Connexion,
  },


  /*----- for in app --------------------------*/
  {
    path: '/welcome',
    name: 'welcome',
    component: Welcome,
    meta: {
    //  middleware: [loading]
    }
  },
  {
    path: '/menu',
    name: 'landingPage',
    component: LandingPage,
    meta: {
    //  middleware: [isLoaded]
        middleware: [loading]
    }
  },
  {
    path: '/locataires',
    name: 'locataires',
    component: Locataires,
    meta: {
      middleware: [isLoaded]
    }
  },
  {
    path: '/locataire/:locId',
    name: 'locataire',
    component: Locataire,
    meta: {
      middleware: [isLoaded]
    }
  },
  {
    path: '/locataires/form',
    name: 'locataires/form',
    component: LocatForm,
    meta: {
      middleware: [isLoaded]
    }
  },
  {
    path: '/cites',
    name: 'cites',
    component: Cites,
    meta: {
      middleware: [isLoaded]
    }
  },
  {
    path: '/contrat',
    name: 'contrat',
    component: ContratForm,
    meta: {
      middleware: [isLoaded]
    }
  },
  {
    path: '/payment',
    name: 'payment',
    component: PaymentForm,
    meta: {
      middleware: [isLoaded]
    }
  },
  {
    path: '/chargeForm',
    name: 'chargeForm',
    component: ChargeForm,
    meta: {
      middleware: [isLoaded]
    }
  },
  {
    path: '/cites/chambres/:citeKey',
    name: 'chambres',
    component: Chambres,
    meta: {
      middleware: [isLoaded]
    }
  },
  {
    path: '/login',
    name: 'login',
    component: Login,
    /*meta: {
      middleware: [isLoaded]
    }*/
  },
  {
    path: '/compte',
    name: 'compte',
    component: Compte,
    meta: {
      middleware: [isLoaded]
    }
  },
  {
    path: '/facture/via/:via/ctId/:ctId/payId/:payId',
    name: 'facture',
    component: Facture,
    meta: {
      middleware: [isLoaded]
    }
  },
  {
    path: '/payments',
    name: 'payments',
    component: Payments,
    meta: {
      middleware: [isLoaded]
    }
  },
  {
    path: '/charge',
    name: 'charge',
    component: Charge,
    meta: {
      middleware: [isLoaded]
    }
  },
  {
    path: '/recapForm',
    name: 'recapForm',
    component: RecapForm,
    meta: {
      middleware: [isLoaded]
    }
  },
  {
    path: '/recap/annee/:annee/mois/:mois/moisId/:moisId/cite/:cite/citeId/:citeId',
    name: 'recap',
    component: Recap,
    meta: {
      middleware: [isLoaded]
    }
  },
  {
    path: '/help',
    name: 'help',
    component: Help,
    meta: {
      middleware: [isLoaded]
    }
  },
  {
    path: '/support',
    name: 'support',
    component: Support,
    meta: {
      middleware: [isLoaded]
    }
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  //history: createWebHashHistory(process.env.BASE_URL),
  routes
})

export default router
