<template>
<section class="lPblicationForm" >
    <div  style="z-index:3" class="lPBFCTitle">
        <div class="lPBFCCross" @click="$router.go(-1)"><img src="@/assets/Retour.png"/></div>
        Créer Nouveau contrat
    </div>
    <div class="lPBFCBody">
            <div class="lPBFCinput">
                <span>Locataire</span>
                <LDropDown style="z-index:2;" ref='locataire' :elements="locataires" maxHeight="110px" maxWidth="100%" />
            </div>
            <div class="lPBFCinput">
                <span>Cite</span>
                <LDropDown @selected='setChambres' style="z-index:1;" :elements="cites" maxHeight="110px" maxWidth="100%" />
            </div>
            <div class="lPBFCinput">
