                <input class="lPBFCinputStyle" placeholder="ex: eau, cable, aucune" type="text" v-model="descripCharges"/>
            </div>
            <div class="lPBFCinput">
                <span>Paye en début de mois</span>
                <input type="checkbox" v-model="startMonthPaying"/>
            </div>
            <div class="lPBFCinput">
                <span>Date début</span>
                <input class="lPBFCinputStyle" type="date" v-model="dateDebut"/>
            </div>
            <div class="lPBFCButton" @click='showYesNo()' ><span>Enregistrer</span></div>
    </div>
