import EditMenu from '@/components/global/editMenu/EditMenu'
import YAlert from '@/components/global/yAlert/YAlert'
import LLoading1 from '@/components/global/lLoading1/LLoading1'
export default {
    name: 'locataire',
    components: {
        EditMenu,
        YAlert,
        LLoading1
    },
    data () {
        return {
            loading: false,
            newForm: false,
            newLogin: '',
            newPassword: '',
            newRole: 'secretaire',
            newTel:'',
            comptes: [],
            cities: [],
            //------- modifier ----------
            form: false,
            password: '',
            login: '',
            tel:''
        }
    },
    methods: {
        add () {
            if(this.newLogin == '' || this.newPassword == '' || this.newTel == '') {
                this.$refs.yAlert.display('Veuillez remplir les champs')
                return
            }

            let i = 0, result = []
            for(i=0;i<this.cities.length;i++) {
                if(this.cities[i].chosen) result.push(this.cities[i].id)
            }
            if (result.length == 0) {
                this.$refs.yAlert.display('Un compte doit avoir accès à au moins une cité')
                return
            }

            this.loading = true
            const axios = require('axios')
            axios.post(this.$store.state.baseUrl + 'inscription.php', {
                type: 'addCompte',
                login: this.newLogin,
                password: this.newPassword,
                tel: this.newTel,
                role: this.newRole,
                cities: result
            })
            .then(({data}) => {
                this.loading = false
                if(data == 'exist') {
                    this.$refs.yAlert.display('Données déjà prix')  
                }
                else {
                    this.$refs.yAlertS.display('Fait !')
                    this.settingCompte(data)
                    //------ reinitialize ----------------------
                    this.newLogin = ''
                    this.newAdmin = false
                    this.newPassword = ''
                    this.newForm = false
                }
            })
            .catch((error) => {
                console.log(error)
                this.loading = false
            })
        },
        getCompte () {
            this.loading = true
            const axios = require('axios')
            axios.post(this.$store.state.baseUrl + 'inscription.php', {
                type:'get',
            })
            .then(({data}) => {
                this.settingCompte(data)

                this.loading = false
            })
            .catch((error) => {
                console.log(error)
                this.loading = false
            })
        },
        modify () {
            if(this.login == '' || this.password == '' || this.tel == '') {
                this.$refs.yAlert.display('Veuillez remplir les champs')
                return
            }
            this.loading = true
            const axios = require('axios')
            axios.post(this.$store.state.baseUrl + 'inscription.php', {
                type:'modify',
                login: this.login,
                tel:this.tel,
                password: this.password
            })
            .then(({data}) => {
                this.loading = false
                if(data == 'exist') {
                    this.$refs.yAlert.display('Données déjà prix')  
                }
                else {
                    this.$store.commit('loginCommit',{login:this.login,isAdmin:1})
                    //--- closure ----------
                    this.login = ''
                    this.password = ''
                    this.form = false
                    this.$refs.yAlertS.display('Fait !')
                }
                
            })
            .catch((error) => {
                console.log(error)
                this.loading = false
            })
        },
        deleteCompte (compte) {
            this.loading = true
            const axios = require('axios')
            axios.post(this.$store.state.baseUrl + 'inscription.php', {
                type:'delete',
                id:compte.id,
                nom: compte.nom,
                tel:compte.tel,
                password:compte.password
            })
            .then(({data}) => {
                this.settingCompte(data)
                this.loading  = false
            })
            .catch((error) => {
                console.log(error)
                this.loading = false
            })
        },
        settingCompte (data) {
            //-----setting comptes--------------
            let i = 0, result = {}
            for(i=0;i<data.length;i++) {
                if(typeof result[data[i].id] === 'undefined' ) {
                    result[data[i].id] = {...data[i],cities:[]}
                }
                let j=0
                for(j=0;j<this.cities.length;j++) {
                    if(data[i].cite == this.cities[j].id) {
                        result[data[i].id].cities.push({...this.cities[j]})
                    }
                }
            }
            this.comptes = result
        }
    },
    mounted () {
        let i,cts = this.$store.state.load.cites, result = []
        for(i=0;i<cts.length;i++) {
            result.push({id:cts[i].id,nom:cts[i].nom,chosen:false})
        }
        this.cities = result
        this.getCompte ()
    }
}