import LDropDown from '@/components/forms/lDropDown/LDropDown.vue'
import LLoading1 from '@/components/global/lLoading1/LLoading1'
import YAlert from '@/components/global/yAlert/YAlert'
import YesNo from '@/components/global/yesNo/YesNo1'
export default {
  name: 'modifyLocataire',
  components: {
    LDropDown,
    LLoading1,
    YAlert,
    YesNo
  },
  data () {
    return {
      Display: false,
      DispForm:false,
      loading: false,
      contrat: [],
      startMonthPaying:'',
      //--------------------
      annees: [{nom:2020},{nom:2021},{nom:2022},{nom:2023},{nom:2024},{nom:2025},{nom:2026},{nom:2027},
        {nom:2028},{nom:2029},{nom:2030},{nom:2031},{nom:2032},{nom:2033},{nom:2034},{nom:2035}
      ],
      mois: [{nom:'Janvier',id:0},{nom:'Février',id:1},{nom:'Mars',id:2},
      {nom:'Avril',id:3},{nom:'Mai',id:4},{nom:'Juin',id:5},{nom:'Juillet',id:6},{nom:'Aout',id:7},
      {nom:'Septembre',id:8},{nom:'Octobre',id:9},{nom:'Novembre',id:10},{nom:'Décembre',id:11}],
      jour:1,
      jourDebut:1,
      description:'',
      montant: '',

      typeForm: 'add'
    }
  },
  methods: {
    showForm (type) {
      this.typeForm = type
      //--- setting up form to last date --------------------
      let vLast = this.contrat.variants[this.contrat.variants.length -1]
      let vLastDebut = (new Date(vLast.startMonth)) 
      this.$refs.anneeDebut.selecting(vLastDebut.getFullYear() - 2020)
      this.$refs.moisDebut.selecting(vLastDebut.getMonth())
      if(type == 'add') {
        this.description = ''
        this.montant = 0
      }
      else {
        this.description = vLast.description
        this.montant = vLast.montant
      }
      this.DispForm = true
    },
    exitForm (event) {
      if(event.target == this.$refs.form) this.DispForm = false
    },
    endMonth(dat) {
      return new Date(dat.getFullYear(),dat.getMonth()+1,0)
    },
    setDayDebut () {
      let d = this.unsetDate(this.$refs.anneeDebut.current.nom,this.$refs.moisDebut.current.id+1,1)
      let j = this.endMonth(new Date(d)).getDate()
      this.jourDebut = j < this.jour ? j : this.jour
    },
    setEndDate (index) {
      let vr = this.contrat.variants
      return vr.length-1 == index ? 'indéterminé' : this.setDate(vr[index+1].startMonth)
    },
    unsetDate (y,m,d) {
      let year = y == '????' ? 2100 : y
      let date = new Date(year+'/'+m+'/'+d)
      //console.log(year+'/'+m+'/'+d)
      //console.log(date)
      return date.getTime()
    },
    setNewIntervall () {
      // let get last variant -----------
      let vr = this.contrat.variants
      let vLast = (this.typeForm == 'add' || vr.length == 1) ?  vr[vr.length -1] : vr[vr.length -2] 
      // compare vLast tu v New --------------------
      let vLastDebut = (new Date(vLast.startMonth)).getTime()
      
      let vNewDebut = this.unsetDate(this.$refs.anneeDebut.current.nom,this.$refs.moisDebut.current.id+1,this.jourDebut)
      
      if(this.description == '' ) {
        alert('Entrez une description des charges pour cette période.')
        return
      }
      if(this.montant === '') {
        alert('Entrez un montant correcte pour cette période.')
        return
      }
      else if(vNewDebut <= vLastDebut ) {
        if(vr.length == 1 && this.typeForm == 'modify') {
          this.modify()
          return
        }
        alert('La date de début ne peut etre inférieur ou égal à la date de début du dernier intervalle')
        return
      }
      else this.typeForm == 'add' ? this.addNew() : this.modify()
    },
    setDate (dat) {
      let d = new Date(dat)
      if(d.getFullYear() == 2100) return 'indeterminé'
      return d.getDate()+' '+this.$store.state.mois[d.getMonth()] +' '+ d.getFullYear()
    },
    display(contrat) {
      this.contrat = contrat
      
      this.startMonthPaying = contrat.payDebutMois == 1 ? true : false
      this.jour = (new Date(this.contrat.date_debut)).getDate()
      // let get last variant -----------
      let vLast = this.contrat.variants[this.contrat.variants.length -1]
      // compare vLast tu v New --------------------
      let vLastDebut = (new Date(vLast.startMonth))
      console.log(vLast.startMonth)
      this.$refs.anneeDebut.selecting(vLastDebut.getFullYear() - 2020)
      this.$refs.moisDebut.selecting(vLastDebut.getMonth())

      this.Display = true
    },
    close () {
      this.loyer = ''
      this.Display = false
    },
    showYesNo () {
      this.$refs.yesNo.display('Voulez vous vraiment éffectuer cette opération ? toutes les données du contrat seront reinitialisées')
    },
    yesNoAction (a) {
      if(a=='yes') this.publish()
    },
    validation () {
      let dn = new Date(this.$store.state.load.serverDate)
      let db = new Date(this.dateDebut)
      let test = db.getTime() - dn.getTime()
      return this.loyer === '' || this.loyer < 0 || isNaN(test)
    },
    addNew () {
      if(this.loading == true) return
      this.loading = true
      const axios = require('axios')
      axios.post(this.$store.state.baseUrl + 'contrat.php', {
        type: 'addVariant',
        description: this.description,
        montant: this.montant,
        dateDebut: this.$refs.anneeDebut.current.nom+'-'+(this.$refs.moisDebut.current.id+1)+'-'+this.jourDebut,
        contrat: this.contrat.id
      })
      .then(({data}) => {
        if(data != 'done') {
          alert('Données incorrecte')
        }
        else {this.DispForm = false
          this.loadData()
        }
      })
      .catch((error) => {
        alert(error)
        this.loading = false
      })
    },
    modify () {
      if(this.loading == true) return
      this.loading = true
      const axios = require('axios')
      axios.post(this.$store.state.baseUrl + 'contrat.php', {
        type: 'modifyVariant',
        description: this.description,
        montant: this.montant,
        dateDebut: this.$refs.anneeDebut.current.nom+'-'+(this.$refs.moisDebut.current.id+1)+'-'+this.jourDebut,
        id: this.contrat.variants[this.contrat.variants.length-1].id,
        contrat: this.contrat.id
      })
      .then(({data}) => {
        this.loading = false
        if(data != 'done') {
          alert('Date de début incorrecte')
        }
        else {
          this.DispForm = false
          this.loadData()
        }
      })
      .catch((error) => {
        alert(error)
        this.loading = false
      })
    },
    deleteVariant () {
      this.loading = true
      const axios = require('axios')
      axios.post(this.$store.state.baseUrl + 'contrat.php', {
        type: 'deleteVariant',
        contrat: this.contrat.id,
        id: this.contrat.variants[this.contrat.variants.length-1].id
      })
      .then(({data}) => {
        if(data != 'done') {
          alert('Impossible de supprimer')
        }
        else {
          this.DispForm = false
          this.loadData()
        }
      })
      .catch((error) => {
        alert(error)
        this.loading = false
      })
    },
    avantFin () {
      this.loading = true
      const axios = require('axios')
      axios.post(this.$store.state.baseUrl + 'contrat.php', {
        type: 'avantFinVariant',
        debutMois: this.startMonthPaying ? 1 : 0,
        contrat: this.contrat.id
      })
      .then(() => {
          this.DispForm = false
          this.loadData()
      })
      .catch((error) => {
        alert(error)
        this.loading = false
      })
    },
    loadData () {
      const axios = require('axios')
      axios.post(this.$store.state.baseUrl + 'update.php', {
        type:'get'
      })
      .then(({data}) => {
        this.$store.commit('loadCommit',data)
        this.loading = false
        this.$refs.yAlertS.display('Enregistré!')
      })
      .catch((error) => {
        console.log(error)
      })
    }
  }
}
