<template>
  <div class="locataire1">
    <div style="z-index:1" class="lPBFCTitle">
        <div class="lPBFCCross" @click="$router.go(-1);"><img src="@/assets/Retour.png"/></div>
        {{ locataire.nom }}
    </div>
    <div class="locat1Body">
      <div class="locat1Info locat1Block">
        <p style="border-bottom:solid 1px rgb(230,230,230);padding-bottom:2px" class="locat1Head">Informations</p>
        <div class="locat1InfoImg">
          <img :src="locataire.image != ''? $store.state.baseUrl+locataire.image : require('@/assets/MesLocataires.png')"/>
        </div>
        <div><div>Nom </div><span>{{ locataire.nom }}</span></div>
        <div><div>Prenom</div><span>{{ locataire.prenom }}</span></div>
        <div><div>Tel 1</div><span>{{ locataire.tel1 }}</span></div>
        <div><div>Tel 2</div><span>{{ locataire.tel2 == '' ? 'Aucun' : locataire.tel2 }}</span></div>
        <div><div>Age</div><span>{{ locataire.age }} ans</span></div>
        <div><div>Situation</div><span>{{ locataire.situation_matrimonial }}</span></div>
      </div>
      <div class="locat1List locat1Block">
        <span style="padding-left:0px;" class="locat1Head"> 
          {{ contrats.length > 0 ? '' : 'Aucune' }} 
          Chambre(s) occupé par {{ locataire.nom }}</span>
        <div v-for="(ctr, index) in contrats" :key="index" class="locat1Contrat">
          <div style="border:none;margin-top:30px"><div>Chambre</div><span>{{ ctr.chambreNom }}</span></div>
          <div><div>Cité</div><span>{{ ctr.citeNom }}</span></div>
          <div><div>Prix/mois</div><span>{{ ctr.loyer }} FCFA</span></div>
          <div class="locat1Limit"><div>Payements début mois</div><span>{{ ctr.payDebutMois == 1 ? 'oui' : 'non' }} </span></div>
          
          <div><div>Dette</div><span style="color:rgb(233, 27, 62)">{{ ctr.newBalance >= 0 ? 0 : ctr.newBalance }} FCFA</span></div>
          <div><div>Solde</div><span>{{ ctr.newBalance >= 0 ? ctr.newBalance : 0 }} FCFA</span></div>
          <div><div>Date d'expiration</div><span>{{ ctr.expirationDate }} </span></div>
          <div class="locat1Limit">
            <div>Temps restant</div>
            <span class="locat1Timer">{{ expirations[index].value }} </span>
          </div>
          
          <!-- <div><div>Nombre mois payé</div><span>{{ ctr.nbrMoisPayer }} </span></div> -->
          <div><div>Date début</div><span>{{ dateFormat(ctr.date_debut) }}</span></div>
          <div><div>Total payé </div><span>{{ ctr.totalPayer }} FCFA</span></div>
          <div v-show="ctr.deleted == 2">
            <div style="color:red">Contrat arreté le:</div>
            <span style="color:red">{{ dateFormat(ctr.stopped) }} FCFA</span>
          </div>
          <span v-show="$store.state.login.role == 'superuser' && ctr.deleted == 0" 
          @click="showMenu(ctr)" class="locat1Menu">Modifier<img src="./img/pencil.svg"/></span>
        </div>
      </div>
    </div>
    <EditMenu :elements='menu'  @select="select" ref="menu" />
    <YesNo ref="yesNo" @option='yesNoAction'/>
    <YesNo ref="yesNo1" @option='yesNoAction1'/>
    <ModifyLocataire ref='modifier' @modified='setContrat' style="z-index:1" />
    <YAlert ref="yAlertS" type="sucess" />
    <LLoading1 :visible="loading"/>
  </div>
</template>
<script src="./locataire.js">
</script>
<style src="./locataire.css">
</style>