<template>
<div v-show='Display' class="yAlert">
  <div class="yAlertSon">
    <div class="yAlLogo" :style="{backgroundColor:leftColor}">
      <img @click="displaying" :src="require('./img/'+ logo + '.svg')">
    </div>
    <div :style="{backgroundColor:rightColor}">
        {{ message }}
    </div>
    <div @click="displaying" class="yAlCross">
      <img src="./img/cross.svg"/>
    </div>
  </div>
</div>
</template>
<style>
.yAlert {
  position: fixed;
  left:0px;
  top:0px;
  width: 100%;
  height: 100%;
  display: flex;justify-content: center;align-items: center;
  z-index: 2;
}
.yAlertSon {
  position: relative;
  width: 290px;
  height: 200px;
  display: flex;
  color: white;
  font-size: 14px;
  font-weight: bold;
  border-radius: 0.5em;
  overflow: hidden;
}
.yAlLogo {
  width: 25%;
  background: blue;
  display: flex;justify-content: center;align-items: center;
}
.yAlLogo img {
  width: 30%;
}
.yAlLogo + div {
  width: 75%;
  display: flex;justify-content: center;align-items: center;
  background-color: skyblue;
  text-align: center;
}
.yAlCross {
  position: absolute;
  width: 30px;
  height: 30px;
  right:0px;
  top:0px;
  margin-top: 5px;
  margin-right: 5px;
  display: flex;justify-content: center;align-items: center;
}
.yAlCross img {
  width: 75%;
}

</style>
<script>
export default {
  name: 'yAlert',
  props: {
    type: {
      type: String,
      default: 'danger'
    },
    text: {
      type: String,
      default: 'some text'
    }
  },
  data () {
    return {
      Display: false,
      message: this.text
    }
  },
  methods: {
    displaying () { 
      this.Display = !this.Display
      this.$emit('closed')
    },
    display(message) {
      this.Display = 'flex'
      this.message = message

    }
  },
  computed: {
    leftColor () {
      return this.type === 'danger' ? 'rgb(191, 49, 41,0.8)':'rgb(35, 153, 48,0.8)'
    },
    rightColor () {
      return this.type === 'danger' ? 'rgb(255, 65, 54,0.8)':'rgb(37, 204, 64,0.8)'
    },
    logo () {
      return this.type === 'danger' ? 'cross':'checked'
    }
  }
}
</script>
