export default {
    name: 'welcome',
    data () {
        return {
            erreur: false,
            login: '',
            password: ''
        }
    },
    methods: {
        validate () {
            const axios = require('axios')
            axios.post(this.$store.state.baseUrl + 'inscription.php', {
                type:'log',
                tel: this.login,
                password: this.password
            })
            .then(({data}) => {
                if(data !== 'nothing') {
                    this.$store.commit('loginCommit',data)
                    this.$router.push('menu')
                }
                else this.erreur = true
            })
            .catch((error) => {
                console.log(error)
            })
          }
    }
}