import LDropDown from '@/components/forms/lDropDown/LDropDown.vue'
import LLoading1 from '@/components/global/lLoading1/LLoading1'
import YAlert from '@/components/global/yAlert/YAlert'
import YesNo from '@/components/global/yesNo/YesNo1'
export default {
  name: 'paymentForm',
  components: {
    LDropDown,
    LLoading1,
    YAlert,
    YesNo
  },
  data () {
    return {
      loading: false,
      locataires: this.$store.state.load.locataires.length == 0 ? [{nom:''}] :this.$store.state.load.locataires,
      chambres: [{nom:''}],
      contrats: [{nom:''}],
      contrat: [],
      loyer: '',
      payment: 0,
      dateDebut: '',
      newBalance: 0,
      payDate:'',
      id: 0 // id received
    }
  },
  methods: {
    validation () {
      let d = new Date()
      d.setHours(0,0,0,0)
      let d1 = new Date(this.payDate)
      d1.setHours(0,0,0,0)
      console.log(d.getTime())
      console.log(d1.getTime())
      if((d1.getTime()<d.getTime()) ) {
        return true
      }
      let result = this.payment === ''  || this.$refs.chambre.current.nom === '' || this.payment < 0 || this.payDate == ''
      return result
    },
    showYesNo () {
      this.$refs.yesNo.display('Voulez vous vraiment éffectuer cette opération ? C\'est une opération critique')
    },
    yesNoAction (a) {
      if(a=='yes') this.publishing()
    },
    setChambres (index) {
      let locataires = this.$store.state.load.locataires
      let locataire = locataires[index]
      let contrats = this.$store.state.load.contrats

      //--- getting corresponding chambre --------------------
      let i, result = [], result1 = [], max = contrats.length
      for(i=0;i<max;i++) {
          if(contrats[i].locataire === locataire.id) {
            result1.push(contrats[i])
            result.push({nom:contrats[i].chambreNom+', cité: ' + contrats[i].citeNom})
          }
      }
      this.chambres = result.length > 0 ? result : [{nom:''}]
      this.contrats = result1.length > 0 ? result1 : [{nom:''}]
      if(result.length > 0) this.setPrice(0)
    },
    setPrice (index) {
      this.contrat = this.contrats[index]
      this.newBalance = this.setBalance(this.contrat)
    },
    publishing (){
      if(this.validation()) {
        this.$refs.yAlert.display('Veuillez remplir tous les champs correctement!')
        return
      }
      this.loading = true
      const axios = require('axios')
      axios.post(this.$store.state.baseUrl + 'contrat.php', {
        type: 'pay',
        manager: this.$store.state.login.id,
        managerName: this.$store.state.login.login,
        contrat: this.contrat.id,
        payment: this.payment,
        date: this.payDate
      })
      .then(({data}) => {
          this.id = data
          console.log(data)
          this.loadData()
        })
        .catch((error) => {
          console.log(error)
        })
    },
    loadData () {
      const axios = require('axios')
      axios.post(this.$store.state.baseUrl + 'update.php', {
        type:'get'
      })
      .then(({data}) => {
        this.$store.commit('loadCommit',data)
        this.loading = false
        this.$refs.yAlertS.display('Fait!')
      })
      .catch((error) => {
        console.log(error)
      })
    },
    toFacture () {
      this.$router.push({name: 'facture', params: {via:'paymentForm',ctId:this.contrat.id,payId:this.id}})
    },
    endMonth(date) {
      return new Date(date.getFullYear(),date.getMonth()+1,0)
    },
    setBalance (ct) {
      if(!ct.id) return 0
      let dn = new Date(this.$store.state.load.serverDate)
      let db = new Date(ct.date_debut)
      if(dn.getTime() < db.getTime()) {
          return ct.totalPayer + ct.totalCharge // the contract did nont begin
      }
      let c = true, monthElapsed = 0
      while(c){
          let day = db.getDate()
          db.setMonth(db.getMonth()+1)
          let t = new Date(db.getTime())
          if(day != db.getDate()) {
              t.setMonth(t.getMonth()-1)
              t = this.endMonth(t)
          }
          if(db.getTime() >= dn.getTime()) {
              c = false
              break
          }
          else monthElapsed ++
      }
      //console.log('monthElapsed: ' + monthElapsed)
      return ct.totalPayer - monthElapsed*ct.loyer + ct.totalCharge
    },
  },
  mounted () {
    this.setChambres(0)

    let k = this
    let input = document.getElementById('payToPay')
    input.addEventListener('input',function(/*event*/){
        /*console.log(event.data)*/
        let amount = isNaN(parseInt(k.payment)) || k.payment === null ? 0 : parseInt(k.payment)
        k.newBalance = parseInt(k.setBalance(k.contrat)) + parseInt(amount)
    })
  }
}
