<template>
<section v-show="Display" class="lPblicationForm" >
    <div class="lPBFCTitle">
        <div class="lPBFCCross" @click="close"><img src="@/assets/Retour.png"/></div>
        Modifier contrat
    </div>
    <div class="lPBFCBody">
        <div class="lPBFCinput">
                <span>Paye en début de mois</span>
                <input type="checkbox" @change="avantFin" v-model="startMonthPaying"/>
        </div>
        <div class="lPBFCinput" style="flex-direction:column; align-items:flex-start;">
            <span>Montant des charges</span>
            <div v-for="(vr,index) in contrat.variants" :key="index" class="lPBFVariants">
                <span>De {{ setDate(vr.startMonth) }} à {{ setEndDate(index) }}</span>
                <div>Montant<span> {{ vr.montant }} FCFA</span></div>
                <div>Raison<span> {{ vr.description }} </span></div>
                <p v-show="index == contrat.variants.length - 1" class="lPBFVButton">
                    <span @click="showForm('modify')" >Modifier</span>
                    <span @click="deleteVariant" v-show="index!=0">Supprimer</span>
                </p>
            </div>
            <p class="lPBFVMore"><span @click="showForm('add')">Ajouter un autre intervalle</span></p>
        </div>
    </div>
    <div v-show="DispForm" @click="exitForm" ref="form" class="lPBFVForm">
        <div>
            <h3> {{ typeForm == 'add' ? 'Ajouter un ' : 'Modifier l\'' }}intervalle </h3>
            <div class="lPBFVDesc">
                <span>Description</span>
                <input placeholder="description" v-model="description" type="text"/>  
            </div>
            <div class="lPBFVDesc">
                <span>Montant</span>
                <input placeholder="0" v-model="montant" type="number"/>  
            </div>
            <div v-show="!(typeForm=='modify'&&contrat.variants.length==1)" class="lPBFVDrop">
                <span>date de début</span>
                <div>
                    {{ jourDebut }}
                    <LDropDown style="z-index:1;" @selected="setDayDebut" ref='moisDebut' :elements="mois" maxHeight="110px" maxWidth="120px" />
                    <LDropDown style="z-index:1;" @selected="setDayDebut" ref='anneeDebut' :elements="annees" maxHeight="110px" maxWidth="90px" />
                </div>
            </div>
            <p class="lPBFVButtonF"><span @click="setNewIntervall">Enregistrer</span></p>
        </div>
    </div>
    <LLoading1 :visible="loading"/>
    <YAlert ref="yAlert"/>
    <YAlert @click="close" ref="yAlertS" type="sucess" />
    <YesNo ref="yesNo" @option='yesNoAction'/>
</section>
</template>
<style src="./locatForm.css">
</style>
<script src = "./modifyLocataire.js">
</script>
