export default {
  name: 'lDropDown',
  props: {
    bdColor: {
      type: String,
      default: 'solid 1px black'
    },
    maxHeight: {
      type: String,
      default: '300%'
    },
    maxWidth: {
      type: String,
      default: '120px'
    },
    elements: {
      type: Array,
      default:[{nom:''}]
    }
  },
  data () {
    return {
      vheight: '0px',
      isCollapsed: false,
      current: this.elements[0]
    }
  },
  methods: {
    heightCollapse () {
      this.vheight = this.isCollapsed ? '0px' : this.maxHeight
      this.isCollapsed = !this.isCollapsed
    },
    deCollapse () {
      this.vheight = '0px'
      this.isCollapsed = !this.isCollapsed
    },
    collapse () {
      this.vheight = this.maxHeight
      this.isCollapsed = !this.isCollapsed
    },
    selecting (index) {
      this.current = this.elements[index]
      this.deCollapse()
      this.$emit('selected',index)
    },
    exist(a) {
      if(typeof a === 'undefined') return ''
      return a
    }
  },
  watch: {
    elements (newVal) {
      this.current = newVal[0]
    }
  }
}
