export default {
    name: 'recap',
    data () {
        return {
            contrat: this.$store.state.load.contrats,
            annee: '',
            mois: '',
            moisId:'',
            cite: '',
            contrats: [],
            payments: [],
            charges:[],
            visible: {locataire:true,cite:true,chambre:true,payements:true,dette:true,
                    avance:true,restant:true,impaye:true},
            totalPayment:0,
            totalCharge:0,
            totalVariant:0,
            totalVariant1:0, // totalVariant when mois = tous
            maxTime:0,
            display:{cite:true,chambre:true,charge:true,avance:true, 'paye en avance':true,
            'mois restant':false,'mois impayé':false}
        }
    },
    computed: {
        citeC () {
            return this.cite != 'Toutes' ? this.cite : 'toutes les cités'
        },
        moisC () {
            return this.mois !='Tous' ? this.mois : ''
        },
        width () {
            let i=0
            for(const prop in this.display) {
                if(!this.display[prop]) i++
            }
            return (810 - 90*i)+'px'
        }
    },
    methods: {
        goBack() {
            this.$router.go(-1)
        },
        endMonth(date) {
            let d = new Date(date.getFullYear(),date.getMonth()+1,0)
            d.setHours(23,59,59,0)
            return d
        },
        nextMonth(d,day) {
            d.setDate(1)
            d.setMonth(d.getMonth()+1)
            let b = this.endMonth(d)
            if (b.getDate()<day) d = b
            else  d.setDate(day)
            return d
        },
        previousMonth(d,day) {
            d.setDate(1)
            d.setMonth(d.getMonth()-1)
            let b = this.endMonth(d)
            if (b.getDate()<day) d = b
            else  d.setDate(day)
            return d
        },
        getPayment (payment) {
            if(payment.length == 0) return []
            // -- getting max time
            let dn = new Date()
            dn.setFullYear(this.annee)
            dn.setMonth(this.moisId)
            dn.setDate(1)
            dn.setHours(0,0,0,0)
            let minTime = dn.getTime()
            dn = this.endMonth(dn)
            dn.setHours(23,59,59,0)
            let maxTime = dn.getTime()

            // toute
            if(this.mois == 'Tous') {
                dn.setFullYear(this.annee,0,1)
                dn.setHours(0,0,0,0)
                minTime = dn.getTime()
                dn.setFullYear(this.annee,11)
                dn = this.endMonth(dn)
                dn.setHours(23,59,59,0)
                maxTime = dn.getTime()
            }

            let result = []
            let i, max = payment.length
            for(i=0;i < max;i++) {
                let date = new Date(payment[i].date)
                if(date.getTime() <= maxTime && date.getTime() >= minTime) {
                    let date = new Date(payment[i].date)
                    result.push({day:date.getDate(), month:date.getMonth(),
                        montant:payment[i].montant, id:payment[i].id
                    })
                }
            }
            return result
        },
        getContrat (contrat) {
            if (contrat.length == 0) return []
            // -- getting max time
            let dn = new Date()
            dn.setFullYear(this.annee)
            dn.setMonth(this.moisId)
            dn.setDate(1)
            dn.setHours(0,0,0,0)
            let minTime = dn.getTime()
            dn = this.endMonth(dn)
            dn.setHours(23,59,59,0)
            let maxTime = dn.getTime()

            // toute
            if(this.mois == 'Tous') {
                dn.setFullYear(this.annee,0,1)
                dn.setHours(0,0,0,0)
                minTime = dn.getTime()
                dn.setFullYear(this.annee,11)
                dn = this.endMonth(dn)
                dn.setHours(23,59,59,0)
                maxTime = dn.getTime()
            }

            let result = []
            let i, max = contrat.length
            for(i=0;i < max;i++) {
                let date = new Date(contrat[i].date)
                if(date.getTime() <= maxTime && date.getTime() >= minTime) {
                    let date = new Date(contrat[i].date)
                    result.push({day:date.getDate(), month:date.getMonth(),
                        montant:contrat[i].montant, id:contrat[i].id,
                        reason:contrat[i].raison
                    })
                }
            }
            return result
        },
        totalPayer (ct,maxTime) {
            let result = 0
            let i, payment = ct.payments, max = payment.length
            for(i=0;i < max;i++) {
                let date = new Date(payment[i].date)
                if(date.getTime() <= maxTime) {
                    result +=payment[i].montant
                }
            }
            return result
        },
        totalCharger (ctId,maxTime) {
            let result = 0
            let i, charge = this.charges, max = this.charges.length
            for(i=0;i < max;i++) {
                let date = new Date(charge[i].date)
                if(charge[i].contrat == ctId && date.getTime() <= maxTime) {
                    result +=charge[i].montant
                }
            }
            return result
        },
        totalVarier (ct,maxTime,debut) {
            let a = new Date(debut)
            let day = a.getDate()
            let totalVariant = 0, variant = 0
            let j = 0, vr = ct.variants, max = ct.variants.length
            for(j=0;j<max;j++) {
                //---- let set the end of this variant which is the start of the next variant ----

                let end = j == (max-1) ? new Date('2100-1-1') : new Date(vr[j+1].startMonth)
                end.setHours(23,59,59,0)

                if(a.getTime() > maxTime ) break
                if(j>0) a = this.previousMonth(a,day)
                let c=true
                while(c) {
                    //-- a = la date de fin après elapsed mois écoulés ----
                    
                    // console.log('-----debut----'+a)
                    a = this.nextMonth(a,day)
                    if(a.getTime() >= end.getTime() || a.getTime() > maxTime) {
                        c = false
                        break
                    }
                    //console.log(a+'----fin-------------')
                    
                    totalVariant +=vr[j].montant
                    variant = vr[j].montant
                }
            }

            this.totalVariant += totalVariant
            this.totalVariant1 += variant
            return {totalVariant:totalVariant, variant:variant }
        },
        setBalance (ct) {
            let stopped = new Date(ct.stopped)
            let maxTime = stopped.getTime() < this.maxTime ? stopped.getTime() : this.maxTime
            let totalPay = this.totalPayer(ct,maxTime)
            let totalCharge = this.totalCharger(ct.id,maxTime)
            
            let db = new Date(ct.date_debut)
            db.setHours(0,0,0,0)
            let day = db.getDate()
            db = ct.payDebutMois == 1 ? this.previousMonth(db,day) : db
            let vrs = this.totalVarier(ct,maxTime,db)
            ct.totalVariant = this.mois == 'Tous' ? vrs.totalVariant : vrs.variant
            
            let monthElapsed = 0, c = true
            while (c) {

                db = this.nextMonth(db,day)
                //console.log(this.maxTime)
                if(db.getTime() > maxTime) {
                    c = false
                    break
                }
                monthElapsed ++
            }
            return totalPay - monthElapsed*ct.loyer + totalCharge - vrs.totalVariant
        },
        moisRestant (b,loyer) {
            return b < 0 ? 0 : (b - b%loyer)/loyer
        },
        moisImpayer (b,loyer) {
            return b >= 0 ? 0 : -((-b - (-b)%loyer)/loyer) - ((-b)%loyer > 0 ? 1 : 0)
        },
        dette (b) {
            return b < 0 ? b : 0
        },
        avance (b) {
            return b > 0 ? b : 0
        },
        totalDette () {
            let i, result = 0
            for(i=0;i<this.contrats.length;i++) {
                result +=this.dette(this.contrats[i].balance)
            }
            return result
        },
        totalAvance () {
            let i, result = 0
            for(i=0;i<this.contrats.length;i++) {
                result +=this.avance(this.contrats[i].balance)
            }
            return result
        },
        totalMoisR () {
            let i, result = 0
            for(i=0;i<this.contrats.length;i++) {
                result +=this.moisRestant(this.contrats[i].balance,this.contrats[i].loyer)
            }
            return result
        },
        totalMoisI () {
            let i, result = 0
            for(i=0;i<this.contrats.length;i++) {
                result +=this.moisImpayer(this.contrats[i].balance,this.contrats[i].loyer)
            }
            return result
        },
        toFacture (ctId,payId) {
            this.$router.push({name: 'facture', params: {via:'recap',ctId:ctId, payId:payId}})
        },
        init () {
            this.annee = this.$route.params.annee
            this.mois = this.$route.params.mois
            this.moisId = this.$route.params.moisId
            this.cite = this.$route.params.cite
            let citeId = this.$route.params.citeId
            

            let dn = new Date()
            dn.setFullYear(this.annee)
            dn.setMonth(this.moisId)
            dn = this.endMonth(dn)
            dn.setHours(23,59,59,0)
            if(this.mois == 'Tous') {
                dn.setFullYear(this.annee,11)
                dn = this.endMonth(dn)
            }
            this.maxTime = dn.getTime()

            // getting contrat
            let cts = this.$store.state.load.contrats
            let takeAll = false
            if(this.cite == 'Toutes') {
                takeAll = true
            }
            let i, result=[], m = cts.length
            for(i=0;i<m;i++) {
                if(cts[i].cite == citeId || takeAll ) {
                    result.push({...cts[i],payments:[],charges:[],variants:[],totalVariant:0})
                }
            }
            this.contrats = result


            // setting payments
            let payment = this.$store.state.load.payments
            let j, max = payment.length, max1=this.contrats.length
            let resultPay = []
            for(i=0;i<max;i++) {
                for(j=0;j<max1;j++) {
                    if( payment[i].contratLocation == this.contrats[j].id ) {
                        resultPay.push(payment[i])
                        this.contrats[j].payments.push(payment[i])
                        this.totalPayment +=payment[i].montant
                        break
                    }
                }
            }
            this.payments = resultPay
            // setting charges 
            let charges = this.$store.state.load.charges
            let resultCharge = []
            max = charges.length, max1=this.contrats.length
            for(i=0;i<max;i++) {
                for(j=0;j<max1;j++) {
                    if(charges[i].contrat == this.contrats[j].id ) {
                        resultCharge.push(charges[i])
                        this.contrats[j].charges.push(charges[i])
                        this.totalCharge += charges[i].montant
                        break
                    }
                }
            }
            this.charges = resultCharge
            // setting variants
            let vr = this.$store.state.load.variants
            max = vr.length, max1=this.contrats.length
            for(i=0;i<max;i++) {
                for(j=0;j<max1;j++) {
                    if(vr[i].contrat == this.contrats[j].id ) {
                        this.contrats[j].variants.push(vr[i])
                        break
                    }
                }
            }
            // setting balance
            max1=this.contrats.length
            for(j=0;j<max1;j++) {
                let b = this.setBalance(this.contrats[j])
                this.contrats[j] = {...this.contrats[j],balance:b}
            }
        }
    },
    mounted () {
        this.init()
    }
}