          pieces occupées par celui ci.
          <img class="helpWideImg" src="./img/2.png"/>
          </p>
          <p>
            <b>Chambre CH1: </b> CH1 est le nom de la chambre occupé et <b>NewCity</b> le 
            nom de la cité de la chambre.<br/>
            <b>Date début: </b> la date de début du contrat d'occupation de la chambre par le locataire.<br/>
            <b>Prix/mois: </b> Le montant du loyer. <br/>
            <b>Dette: </b> représente la dette du client, si elle est supérieur à 0 le client a 
