            <b>Solde: </b>Représente l'argent que le client a comme avance.<br/>
            <b>Temps restant: </b>Temps après lequel le client aura des aérées.<br/>
            <b>Date d'expiration: </b> date après laquelle le locataire va devoir de l'argent. <br/>
